.item-wrapper {
	width: 94px;
	margin: 10px 4px 0;
	position: relative;
	height: 100%;
   	min-height: 122px;
    display: inline-block;
    vertical-align: top;
    float: none;
    @include transition(all 0.2s ease-in-out);

    &.item_doble {
    	width: 194px;
    }

	/* iphone 6 */
	@media only screen
	and (min-device-width: 375px)
	and (max-device-width:627px)
	and (-webkit-min-device-pixel-ratio: 2){
		width: 113px;
    	min-height: 150px;

    	&.item_doble {
	    	width: 246px;
	    }
	}

	/* lg g2*/
	@media only screen
	and (min-device-width: 360px)
	and (max-device-width:640px)
	and (-webkit-min-device-pixel-ratio: 2){
		width: 105px;
		min-height: 142px;

    	&.item_doble {
	    	width: 216px;
	    }
	}

    @media (min-width: $screen-sm-min) {
    	width: 115px;
    	min-height: 156px;

    	&.item_doble {
	    	width: 236px;
	    }
    }

    @media (min-width: $screen-md-min) {
    	width: 195px;
    	min-height: 260px;
    	margin: 10px 4px;

    	&.item_doble {
	    	width: 402px;
	    }
    }


    h5 {
    	font-size: 0.875em;
    	margin-bottom: 0px;
    	margin-top: 2px;

    	@media (min-width: $screen-sm-min) {
	    	margin-bottom: 10px;
	    	margin-top: 10px;
    	}
    }

	.item-link {
		display: block;
		color: $text-color;
		word-wrap: break-word;

		@media (min-width: $screen-md-min) {
	    	word-wrap: inherit;
	    }

		.item-img {
			position: relative;
			border: 2px solid $item-img-border;
			@include transition(all 0.2s ease-in-out);

			img {
				color: $text-color;
				background-image: url($img-bg-slides);
				background-size: cover;
				background-color: $bg-img-start;
				background-repeat: no-repeat;
				background-position: center center;
				text-indent: -9999px;
				min-height: 122px;
				display: block;
				opacity: 0;
				@include transition(all 0.2s ease-in-out);


				/* iphone 6 */
				@media only screen
				and (min-device-width: 375px)
				and (max-device-width:627px)
				and (-webkit-min-device-pixel-ratio: 2){
					min-height: 150px;
				}

				/* lg g2*/
				@media only screen
				and (min-device-width: 360px)
				and (max-device-width:640px)
				and (-webkit-min-device-pixel-ratio: 2){
					 min-height: 142px;

				}

				@media (min-width: $screen-sm-min) {
			    	min-height: 150px;
			    	background-size: inherit;
			    }

				@media (min-width: $screen-md-min) {
			    	min-height: 260px;
			    }
			}
		}
	}

	&.item_doble {
		.item-link {
			.item-img {
				img {
					max-height: 122px;

					/* iphone 6 */
					@media only screen
					and (min-device-width: 375px)
					and (max-device-width:627px)
					and (-webkit-min-device-pixel-ratio: 2){
						max-height: 150px;
					}

					/* lg g2*/
					@media only screen
					and (min-device-width: 360px)
					and (max-device-width:640px)
					and (-webkit-min-device-pixel-ratio: 2){
						 max-height: 142px;

					}

					@media (min-width: $screen-sm-min) {
				    	max-height: 150px;
				    	background-size: inherit;
				    }

					@media (min-width: $screen-md-min) {
				    	max-height: 260px;
				    }
				}
			}
		}

	}

	@for $i from 1 through length($colors) {
		&:nth-child(#{length($colors)}n+#{$i}) {
			.item-link {
				.item-img {
					img {
						opacity: 1;
						background-color: nth($colors, $i);
					}
				}
			}
		}
	}

	.item-play {
		opacity: 0;
		position: absolute;
		color: lighten($link-color,10);
		top: 50%;
		left: 50%;
		z-index: 3;
		@include transition(opacity .2s ease);
		@include transform(translate(-50%, -50%));
	}

	@media (min-width: $screen-md-min) {
		&:hover {
	    	@include scale(1.05);
	    	z-index: 9;

			.item-play {
				opacity: 1 !important;
				color: $link-color;
			}

			.item-img {
				border: 2px solid $item-img-border-hover;
				@include box-shadow(0 0 5px $link-color);
			}

			.item-link {
				text-decoration: none;
				color: $link-color;
			}

			.detail & {
				@include scale(1.00);

				.item-img {
					border: none;
					@include box-shadow(0 0 0 transparent);
				}
			}
	    }
    }

	&:active, &:focus {
		border: 0;
		outline: none;
	}

	img {
		width: 100%;
	}

}

#grid-result-list {
	.item-wrapper {
		margin: 10px 5px 0;

		// @media (min-width: $screen-md-min) {
	  //   	margin: 10px 5px;
	  //   }
	}
}

/* Estilos para carruseles de temporadas */
.seasons {
    .slick:not(.slick-initialized) .item-wrapper {
        min-height: auto;
        @media screen and (max-width: $screen-sm-min ) {
	        width: 45%;
    	}

        .item-link .item-img img {
            min-height: auto;
        }
    }

    .slick .item-wrapper {
        min-height: auto;

        .item-link .item-img img {
            min-height: auto;
        }
	}
}

.item-search {
	float: none;
	display: inline-block;
	vertical-align: top;
}

.item-default {
	color: $text-color;
	background-image: url($img-bg-slides);
	background-color: $bg-img-start;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 75%;
	text-indent: -9999px;
	width: 56px;
	height: 75px;
	display: block;

	&_mobile {
		width: 100%;
		min-height: 130px;
		margin: 0 auto;

		@media screen and (min-width: $screen-sm-min) {
			min-height: 200px;
		}
		@media screen and (min-width: $screen-md-min) {
			min-height: 163px;
		}
	}
}

.shiftLeft {
	@include transition(all 0.2s ease-in-out);
    @include translate(-10px, 0);
}

.shiftRight {
	@include transition(all 0.2s ease-in-out);
    @include translate(10px, 0);
}

.addList, .putOffList {
	display: none;

	.addList-overlay {
		background-color: rgba($black,0.8);
		height: 100%;
		width: 100%;
		position: absolute;
	}

	.addList-checked {
		position: absolute;
	    left: 0;
	    right: 0;
	    margin-left: 0;
	    margin-right: 0;
	    top: 30%;

	    .fa-check {
	    	text-align: center;
	    	display: block;
	    	color: $brand-success;
	    	font-size: 55px;
	    }

	    p {
	    	text-align: center;
	    }
	}
}

.putOffList {

	.addList-checked {

		.fa-check {
			color: $white;
		}
	}
}
