footer{
	width: 100%;
	background-color: $bg-footer;
	//color: $text-color;
	-webkit-box-flex: none;
	-webkit-flex: none;
	-ms-flex: none;
	flex: none;
	font-size: 0.875em;

	ul {
		list-style: none;
		padding: 0;
	}

	.tags .genre-item {
		width: auto;
	} 

}

// [data-locked="true"] footer {
// 	min-width: $container-md;
// }

.email-subscribe-form {
	background-color: $bg-email-subscribe-form;
	padding: 10px 0 0;

	.input-group {
		.form-control {
			height: 50px;
			margin: 10px 5px 0 0;
			border-radius: 6px !important;
			@include alpha-attribute('background-color', rgba($dark, 0.2), $light);
			border: 0;
			font-size: 1.150em;
			width: 75%;
		}
	}

	&.success {
		background-color: $bg-email-subscribe-form-success;

		.input-group-addon, p {
			display: inline-block;
		}

		p {
			padding-left: 90px;
		}
	}

	&.error {
		background-color: $bg-email-subscribe-form-error;
	}

	.input-group-addon {
		background-color: transparent;
		padding: 0;
		border: 0;
	}

	.input-group-btn {
		height: 50px;
		margin: 12px 5px 0 0;
		display: inline-block;
		width: auto;

		&:last-child {
			.btn-outline{
				border-radius: 25px;
				padding-left: 45px; 
				padding-right: 45px; 
			}
		}
	}

	.btn-outline {
		font-size: 14px; //nedded
	}
}

.footer-links {
	li {
		color: $footer-link-color;
		font-family: $footer-links-font; 
	}

	a {
		padding: 3px 0;
	}

	@media (min-width: $screen-md-min) {
		padding: 20px 0 30px;
	}

	.tags {
		a {
			.label-default {
				color: $link-color;
				font-size: 0.875em;

				&:hover {
					color: $link-hover-color;
				}

				&:active, &:focus {
					color: $footer-link-active-color;
				}
			}
		}
	}
}

.mobile-footer-links {
	li {
		border-bottom: 1px solid $mobile-footer-border;

		@media screen and (min-width: $screen-sm-min){
			text-align: center;
		}

		&:last-child {
			border-bottom: 0 none;
		}
		
		a, span {
			display: block;
			padding: 15px;
		}
	}
}

.subfooter {
	overflow: hidden;
	padding: 25px 0 0;
	background: $bg-subfooter;

	ul {

		li {
			display: inline-block;
		}
	}

	.copyright {
		font-size: 0.875em;
		font-family: $copyright-font;

		img {
			width: 65px;
			vertical-align: sub;
		}
	}

	.col-xs-10.col-sm-8.col-sm-offset-2.text-center, .row .col-xs-12.text-center {
		text-align: left !important;
		@media (min-width: $screen-sm-min) {
			text-align: center !important;
		}
	}

	.data-fiscal {
		display: block;
		width: 35px;
		float: right;
		margin: -15px 0;

		img {
			width: 100%;
		}
	}
}

.subfooter-links {
	padding: 0 0 15px;
	margin-top: -5px;
	font-size: 0.813em;
	@media only screen and (min-width: $screen-sm-min){
		font-size: 0.875em;
	}
	@media only screen and (min-width: $screen-md-min){
		font-size: 1em;
	}

	li {
		display: inline-block;
		margin: 0 5px;

		a {
			@include alpha-attribute('background-color', rgba($light, 0.2), $dark);
			text-align: center;
			line-height: 1.6;
			padding: 5px;
			border-radius: 50%;
			color: $text-color;
			width: 30px;
			height: 30px;
			display: block;

			i {

				@media only screen and (min-width: $screen-md-min){
					vertical-align: 0;
				}
				

				&.fa-youtube-play {
	    			left: -1px;
				    position: relative;
				}
			}

			@media (min-width: $screen-sm-min) {
				padding: 10px;
				width: 40px;
				height: 40px;
			}
		}

		&.facebook {
			a:hover {
				background-color: $facebook-icon-hover-color;
			}
		}

		&.twitter {
			a:hover {
				background-color: $twitter-icon-hover-color;
			}
		}

		&.youtube {
			a:hover {
				background-color: $youtube-icon-hover-color;
			}
		}

		&.pinterest {
			a:hover {
				background-color: $pinterest-icon-hover-color;
			}
		}

		&.instagram {
			a:hover {
				background-color: $instagram-icon-hover-color;
			}
		}
	}
}