.c-menu {
    position: fixed;

    //z-index: -200;
    z-index: 200;
    transition: transform 0.5s;

    //visibility: hidden;
}

.c-menu--slide-left {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
}

@media all and (min-width: 320px) {
    .c-menu--slide-left {
        width: 250px;
    }
}

.c-menu--slide-left .c-menu__close {
    display: block;
    padding: 12px 24px;
    width: 100%;
}

/**
 * Slide/Push Menu Left.
 */
.c-menu--slide-left {
    top: 0;
    left: 0;

    @include translate3d(-100%, 0, 0);
}

@media all and (min-width: 320px) {
    .c-menu--slide-left {
        @include translate3d(-300px, 0, 0);
    }
}

.c-menu--slide-left.is-active {
    @include translate3d(0, 0, 0);

    //z-index: 200;
    //visibility: visible;
}

/**
 * Body states.
 *
 * When a menu is active, we want to hide the overflows on the body to prevent
 * awkward document scrolling.
 */
body.has-active-menu {
    overflow: hidden;
}

/**
 * Close button resets.
 */
.c-menu__close {
    color: #fff;
    background-color: #3184a1;
    font-size: 14px;
    border: none;
    box-shadow: none;
    border-radius: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    cursor: pointer;
}

.c-menu__close:focus {
    outline: none;
}

/* -----------------------------------------------------------------------------

  MASK COMPONENT

----------------------------------------------------------------------------- */
.c-mask {
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    overflow: hidden;
    width: 0;
    height: 0;
    opacity: 0;
    background: $c-mask-bg;
}

.c-mask.is-active {
    width: 100%;
    height: 100%;
    opacity: 0.8;
}

.navmenu-fixed-left {
    text-align: left;
    background-color: $navmenu-fixed-left-color;

    @include box-shadow(1px 1px 8px 2px rgba(0, 0, 0, 0.3));

    a {
        color: $navmenu-fixed-left-link-color;
    }

    .navbar__explore-button {
        display: block;
    }

    .navmenu-fixed-left__profile {
        text-align: left;
        padding: 19px 10px 19px 16px;
        height: 150px;
        background-color: $navmenu-profile-bg;
        background-image: url($img-mobile-profile-nav);
    }

    .login-mobile {
        li {
          padding: 5px 0;
        }
    }

    .user-menu {
        text-align: left;

        a {
            display: block;
        }
    }

    .genre-item {
        width: auto;
    }

    .genre-item .label {
        padding: 0;
        font-family: inherit;
        border-radius: 0;
        display: block;
        text-align: left;
        font-size: 100%;

        &:hover {
            //color: $genre-item-label-hover-color;
            color: $navmenu-fixed-left-link-color;
        }
    }

    .profile-avatar {
        width: 67px;
        height: 67px;
        border-radius: 100%;
        display: block;
        margin-left: 0;
        margin-bottom: 21px;
    }

    .btn-login, .btn-register {
        font-family: $font-family-base;
        padding: 15px !important;
        margin: 0;
        display: block;
        background-color: transparent;
        border: 0 none;
        text-align: left;
        font-size: 0.875em;
        line-height: 1;
        border-radius: 0;
        //text-transform: capitalize;

        &.btn-free-days {
          @extend .btn-primary;
          @extend .btn;
          margin: 7px 5px;
          padding: 5px 30px!important;
        }

        &:hover, &:focus, &:active, &:active:hover {
            text-decoration: none;
            border: 0 none;
        }
    }

    .btn-login-out {
        font-family: $font-family-base;
        border-top: 1px solid $btn-login-out-border-color;

        &:hover, &:focus, &:active, &:active:hover {
            border-top: 1px solid $btn-login-out-border-color;
        }
    }

    .panel i:not(.icon-flip),
    .btn-login-out i,
    .btn-register i,
    .btn-login i {
        width: 33px;
    }

    .icon-flip {
        margin-left: 10px;
    }

    .panel-group {
        margin-bottom: 0;

        .panel + .panel {
            margin-top: 0;
        }
    }

    .navbar-profile-link {
        display: block;
        line-height: 1;
    }
}

.profile-user-options {
    position: relative;
    padding: 0 40px 0 0;
}

.change-user-btn {
    position: absolute;
    right: 0;
    top: -13px;
    padding: 16px;
}
