//
// Typography
// --------------------------------------------------


// Headings
// -------------------------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  color: $headings-color;
  line-height: $headings-line-height;
  margin-top: $line-height-computed;
  margin-bottom: ($line-height-computed / 2);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  small,
  .small {
    font-weight: normal;
    line-height: 1;
    color: inherit;
  }
}

h1, .h1 {
  font-family: $h1-headings-font-family;
  font-size: $font-size-h1-mobile;
  font-weight: $h1-headings-font-weight;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h1-tablet;
  }

  @media (min-width: $screen-lg-min) {
    font-size: $font-size-h1-desktop;
  }
}

h2, .h2 {
  font-family: $h2-headings-font-family;
  font-size: $font-size-h2-mobile;
  font-weight: $h2-headings-font-weight;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h2-tablet;
  }

  @media (min-width: $screen-lg-min) {
    font-size: $font-size-h2-desktop;
  }
}

h3, .h3 {
  font-family: $h3-headings-font-family;
  font-size: $font-size-h3-mobile;
  font-weight: $h3-headings-font-weight;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h3-tablet;
  }
}

h4, .h4 {
  font-family: $h4-headings-font-family;
  font-size: $font-size-h4-mobile;
  font-weight: $h4-headings-font-weight;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h4-tablet;
  }

  .container-user & {
    margin: 0 0 15px 0;

    @media (min-width: $screen-md-min) {
        margin: 0;
    }
  }
}

h5, .h5 {
  font-family: $h5-headings-font-family;
  font-size: $font-size-h5-mobile;
  font-weight: $h5-headings-font-weight;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-h5-tablet;
  }
}

h6, .h6 {
  font-family: $h6-headings-font-family;
  font-size: $font-size-h6-mobile;
  font-weight: $h6-headings-font-weight;
}

h1, .h1,
h2, .h2,
h3, .h3 {
  small,
  .small {
    font-size: inherit;
  }
}

h4, .h4,
h5, .h5,
h6, .h6 {
  small,
  .small {
    font-size: 75%;
  }
}

// Body text
// -------------------------

p {
  margin: 0 0 ($line-height-computed / 2);
}

.lead {
  margin-bottom: $line-height-computed;
  font-size: 1em;
  font-weight: 300;
  line-height: 1.4;

  @media (min-width: $screen-sm-min) {
    font-size: 1em;
  }
}

.detail-summary {
  font-size: 1em;
  line-height: 1.5;
}

.page-header {
  border-bottom: 0;
}

h1.login {
  max-width: 130px;
  margin: 0 auto;
  [data-locked="true"] & {
    width: 50%;
    max-width: inherit;
    margin: 0 auto 60px;
  }
}

.consumption-heading {
  font-size: 1.125em;
  margin: 0;

  &_small {
    font-size: 0.875em;
  }
}

// h5 custom - help-page

.help-page h5{
  font-size: 1.2em;
  line-height: normal;
}

.help-page em{
  font-style: italic;
}

.help-page .sub-list li{
  line-height: 30px;
}

.parental-control span {
  font-family: $parental-font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Página de bienvenida

h1.welcome {
  font-family: $welcome-h1;
  font-size: 48px;
}

h2.welcome {
  font-family: $welcome-h2;
  font-size: 24px;
}
h3.welcome {
  font-family: $welcome-h3;
  font-size: 24px;
}

p.welcome {
  font-family: $welcome-text;
  font-size: 16px;
}

// Activar Suscripción - Método de Pago y para cualquier vista que desea ser usado

p.smoothing, .smoothing {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
}

.italic {
  font-style: italic;
}

// Clase a utilizar cuando se tenga que tachar algun precio o palabra

.strikethrough {
  position: relative;
}

.strikethrough:before {
    position: absolute;
    content: "";
    left: 0;
    top: 50%;
    right: 0;
    border-top: 3px solid;
    border-color: $scooter;
    -webkit-transform: rotate(-5deg);
    -moz-transform: rotate(-5deg);
    -ms-transform: rotate(-5deg);
    -o-transform: rotate(-5deg);
    transform: rotate(-44deg);
    opacity:0;
    transition:opacity 0.5s linear;
}

.strikethrough.active:before {
    opacity:1;
}
