.disclaimer {
	margin-top: 10px;
	margin-bottom: 0;
	font-size: 0.688em;
	text-align: center;

	@media (min-width: $screen-sm-min){
		text-align: left;
		font-size: 0.875em;
		margin-bottom: 10px;
	}

	&_alter {
		margin-bottom: 10px;
		text-align: left;
	}

}

.register-note {
	font-size: 0.875em;
	a {
		padding: 0;
    	line-height: 1;
	}
}

.btn-link-without-padding {
	padding: 0px !important;
}

.btn-link-without-padding-left {
	padding-left: 0px !important;
}

.subscription-cancel--items {
	margin-bottom: 15px;

	input[type=radio] {
		margin: 0 10px 0 0;
	}

	label {
		width: 95%;
	}

	textarea {
		width: 100%;
		min-height: 150px;
		margin: 0 0 15px;
		color: $textarea-color;
	}

}

.disc {
	background-image: url($img-icon-disc);
	background-repeat: no-repeat;
	background-position: 0 7px;
    padding-left: 10px;
    margin-bottom: 6px;
	display: block;
}

.list {
	display: inline-block;
	margin-right: 10px;
	//padding: 3px 5px 3px 0;

	 &.benefits-suscribe {
		margin-right: 0;
	 	font-family: $roboto-regular;
	}
}

.disabled {
	color: $disabled-color;
}

.success {
	color: $success-color;
}

.text-danger {
	color: $error-color;
}

.border-danger {
	border-color: $error-color;
}

.bold {
	font-weight: bold;
}

.visibility-hide {
	visibility: hidden;
}

.visibility-show {
	visibility: visible;
}

.mt0 {
	margin-top: 0;
}


.mb10 {
	margin-bottom: 10px;
}

.mb-20 {
	margin-bottom: 20px;
}

.mb-30 {
	margin-bottom: 30px;
}

.bottom-absolute {
	position: absolute;
	left: 0;
	right: 0;
	bottom: 270px;
}

.form-control.has-success {
	border-color: $success-color;
}

.form-control.error {
  border: 1px solid $error-color;
}
