.form-control {
	font-family: $font-family-base;
	background-color: $bg-form-control;
	border-color: $border-form-control;
	color: $text-color-form-control;
	@include placeholder($color: $input-color-placeholder);
	height: 40px;
	font-size: 1em;

	@media (min-width: $screen-md-min){
		height: $height-form-control;
		font-size: 1.125em;
	}

	&.error {
		color: $text-color-form-control;
	}

	&:focus {
		border-color: $focus-border-form-control;
	}

	&.valid {
		border-color: $form-control-valid;
	}

	#collapse_stripe .form-group & {

		&:focus {
			border-color: $error-color;
		}
	}
}

.stripe-group {
	margin-top: 20px;
}

input#registration_password {

	@media(max-width: $screen-sm-min) {
		font-size: 12px;
	}
}

.form-control[disabled] {
	background-color: $bg-form-disabled;
	color: $color-form-disabled;
	opacity: 0.2;
}

.style-select {
	position: relative;
	background: transparent;

	.form-control {
		background-image: url($img-icon-dropdown);
		background-repeat: no-repeat;
		background-position: 98% 15px;
		font-size: 1em;

		@media (min-width: $screen-md-min) {
			background-position: 98% 21px;
		}

	}

	.select-filter {
	    width: 60%;
	    float: right;
	    margin-top: 14px;
	}

	label.order{
		margin-top: 15px;
		float: left;
		line-height: 3em;

		@media (max-width: 25em){
			font-size: 0.7em;
			line-height: 3.7em;
		}
	}

	option:active, option[selected="selected"] {
		background: $link-hover-color;
	}
}

.change-product-form {
	padding: 10px 0;
	overflow: hidden;

	input[type="radio"] {
		position: absolute;
		opacity: 0;

	}

	label {
		width: 100%;
	}

	[data-locked="true"] & {

		&:checked + .panel-plan {
			@include scale(1.05);
			position: relative;
		}
	}
}


.custom-radio {
	li {
		margin-bottom: 0px;
		max-width: 51%;
		display: inline-block;
		position: relative;

		@media(max-width: $screen-sm-min) {
			max-width: 100%;
		}
	}

	input[type="radio"] {
		position: absolute;
 		visibility: hidden;
	}

	.check{
		display: block;
		position: absolute;
		border: 2px solid #fff;
		border-radius: 100%;
		height: 25px;
		width: 25px;
		top: 14px;
		left: 20px;
		z-index: 5;
		@include transition(border .25s linear);
		cursor: pointer;
	}

	@media(min-width: $screen-sm-min){
		left: 14px;
	}

	li:hover .check:before{
	  	 background: $link-hover-color;
	}

	li .check::before {
		display: block;
		position: absolute;
		content: '';
		border-radius: 100%;
		height: 15px;
		width: 15px;
		top: 3px;
		left: 3px;
		margin: auto;
		@include transition(background 0.25s linear);
	}

	input[type=radio]:checked ~ .check::before{
		background: $link-focus-color;
	}

	label {
		position: relative;
		padding: 8px 0 0 51px;
		margin: 10px auto;
		height: 15px;
		z-index: 9;
		cursor: pointer;
		line-height: 15px;
		font-size: 13px;
		-webkit-font-smoothing: antialiased;
    	font-weight: normal;
 		@include transition(all 0.25s linear);

 		@media(min-width: $screen-xs-min) {
 			font-size: 15px;
 		}

 		@media(min-width: $screen-sm-min) {
			font-size: 17px;	
 		}
 	}

}

.credit-cards {

	li {
		margin-bottom: 0px;
		max-width: 49%;
        width: 13%;
		display: inline-block;
		position: relative;

		@media(min-width: $screen-sm-min){
