.header-detail-mobile {

	background: $bg-header-mobile; /* For browsers that do not support gradients */
    // background: -webkit-linear-gradient($bg-header-mobile 60%, transparent 100%); /* For Safari 5.1 to 6.0 */
    // background: -o-linear-gradient($bg-header-mobile 60%, transparent 100%); /* For Opera 11.1 to 12.0 */
    // background: -moz-linear-gradient($bg-header-mobile 60%, transparent 100%); /* For Firefox 3.6 to 15 */
    // background: linear-gradient($bg-header-mobile 60%, transparent 100%); /* Standard syntax */
    padding: 10px 0;


    .headings {

        h2, h4 {
            font-size: 1.125em;
            margin-top: 0;
            margin-bottom: 0;
            max-width: 170px;
            @media (min-width: $screen-sm-min){
                max-width: 615px;
            }
        }

        h2 {
             @media (min-width: $screen-sm-min){
                font-size: 1.875em;
            }
        }
    }

    .headings, a.btn, .heading-share {
        display: inline-block;
        vertical-align: middle;
    }

    .heading-share {
        float: right;
        margin-right: 15px;
    }

    a.btn {
        color: $text-color !important;
        &:active, &:hover {
            color: $link-color;
        }
    }

    a.btn.dropdown-toggle {
        color: $text-color;
    }
    .open a.btn.dropdown-toggle {
        color: $link-color;
    }

    .channel-headings {
        max-width: 85%;
        overflow: hidden;
        display: inline-block;
        word-wrap: inherit;
    }

}
