.carousel-heading {
	margin: 0;
	overflow: hidden;

	.carousel-title {
		margin-top: 0;
		margin-left: 6px;
		margin-bottom: 0;

		@media (min-width: $screen-sm-min) {
			line-height:28px
		}

		a {
			color: $text-color;

			&:hover {
				color: $link-color;
				text-decoration: none;
			}
		}
	}
}

.title-channel {
	margin: 10px 0 0 5px;
	border: 2px solid $item-img-border;

	a {
		display: block;
		background-color: lighten($bg-body,20);

		img {
			border: 0;
		}

		@media (min-width: $screen-md-min) {
			height: 260px;
		}
	}
}

.slick-channel {
	//overflow: hidden;
}

.title-channel-container, .slick-channel-container {
	display: inline-block;
	vertical-align: top;
}

.title-channel-container {

	width: 48%;
	max-width: 405px;

	@media (min-width: $screen-md-min) {
		width: 405px;
	}

	img {
		width: 100%;
	}
}

.slick-channel-container {
	width: 100%;
	@media only screen and (min-width: 600px) {
		width: 49%;
	}

	/* Landscape for iphone 6 */
		@media only screen
		and (min-device-width: 667px)
		and (-webkit-min-device-pixel-ratio: 2)
		and (orientation: landscape) {
			 width: 100%;
		}


		//galaxy tablet landscape
		@media only screen and
		(min-device-width: 600px)
		and (max-device-width: 1024px)
		and (-webkit-min-device-pixel-ratio: 1)
		and (orientation: portrait) {
			width: 49%;
		}

		@media only screen and
		(min-device-width: 600px)
		and (max-device-width: 1024px)
		and (-webkit-min-device-pixel-ratio: 1)
		and (orientation: landscape) {
			width: 100%;
		}

	@media (min-width: $screen-sm-min) {
		margin-left: 20px;
		width: 47%;
		max-width:  calc(100% - 405px);
	}

	@media (min-width: $screen-md-min) {
		margin-left: 20px;
		width: calc(100% - 438px);
	}
}

.slick, .slick-channel  {
	height: 145px;

	@media (min-width: $screen-sm-min) {
    	height: 170px;
    }

	@media (min-width: $screen-md-min) {
    	height: 275px;
    }

	.slick-list {
		//padding-left: 5px;
		margin-left: 0;
	}

	.slick-arrow {
		z-index: 10;
		height: 28px;
		top: 50%;
	    background-color: transparent;
	    width: 50px;
	    text-shadow: 1px 1px 1px $slick-shadow;

	    @media (min-width: $screen-md-min) {
	    	top: 52%;
	    	height: 280px;
	    }

	    &:before {
	    	font-size: 24px;
	    	font-family: "FontAwesome";
	    	color: $arrow-color;
	    	opacity: 1;
	    	text-shadow: 1px 3px 5px $h1-text-shadow;
	    }

	    [data-locked="true"] & {
	    	background-image: none;
		    background-color: transparent;

		    &:before {
		    	color: transparent;
		    	opacity: 0.75;
		    }

			&:hover, &.hovered {
				@include alpha-attribute('background-color', rgba($link-color, 0.4), $light);

				&:before {
					color: $text-color;
				}
			}

			&.hovered {
				background-image: url($img-slider-arrows);
				background-repeat: no-repeat;
			}
		}
	}

	.slick-prev {
		left: -15px;
		background-position: center left;

		&:before {
			content: '\f053';
			[data-locked="true"] & {
				content: '';
			}
		}
	}

	.slick-next {
		right: 0;
		background-position: center right;

		&:before {
			content:"\f054";
			[data-locked="true"] & {
				content: '';
			}
		}
	}

}

.slick-channel {

	.slick-list {
		margin-left: 15px;

		// @media (min-width: $screen-sm-min) {
		// 	padding-left: 10px;
		// 	margin-left: -5px;
		// }
	}

	.slick-prev {
		left: 0;
	}

	.slick-next {
		right: 0;
	}
}

.slick-slider {
	margin-bottom: 0;
}

.seasons {
	.slick {
		height: inherit;
	}
}
