.navbar-right {
	float: right;
	margin-right: -3px;
    margin-top: -1px;

	@media(min-width: $screen-xs-min) {
		margin-right: 0;
		margin-top: 2px;
	}

	[data-locked="true"] & {
		margin-top: 0;
	}
}

.navbar-left {
	margin-top: 2px;
}

.navbar-header {
	display: inline-block;
	margin-top: 10px;

	@media(min-width: $screen-lg-min){
		margin-top: 0px;
	}

	h1 {
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		padding: 0;

		@media (min-width: $screen-xs-min) {
			margin: 0 0 0 15px;
		}

		@media (min-width: $screen-md-min) {
			vertical-align: middle;
		}

		[data-locked="true"] & {
			vertical-align: middle;
		}
	}
}

.navbar {
	border: 0;
	height: $navbar-min-height;
	margin-bottom: 0;
	color: $navbar-text-color;
	@include transition(top 0.2s ease-in-out);
	background-color: rgba($bg-navbar,0.8);

	.dropdown-menu {
		border-radius: 4px;
	}

	.login-buttons li {
		display: inline-block;
	}


	&_fake {
	    background: transparent;
	    min-height: inherit;
	    padding: 14px 0 20px;

	    .login-buttons {
	    	@media (max-width: $screen-xs-min) {
	    		margin-top: 0;
    		}
	    }

	    .btn {
	    	@media (max-width: $screen-xs-min) {
		    	margin: 0 ;
		    	padding: 5px 6px;
		    	font-size: 11px;
	    	}

	    	@media (min-width: 23.438em) and (max-width: $screen-xs-min) {
    			padding: 5px 13px;
    		}
	    }
	}

	&__form {
		border: 0;
		padding-left: 0;
		margin-top: 10px;
		padding-right: 0;

		@media (min-width: $screen-xs-min) {
			margin-top: 12px;
		}

		[data-locked="true"] & {
			margin-top: 15px;
		}

		.input-group {
			display: inline-block;

			.form-control {
				border: 0;
				height: 31px;
				width: 0;
				background-color: transparent;
				padding: 6px 0;
				float: left;
				font-size: 0.813em;
				@include transition(all 0.5s ease-in-out);

				&:focus {
					@include box-shadow(none);
				}

				&.in {
					width: 101px;
					padding: 6px 10px;
					height: 31px;
					background-color: $bg-form-control;

					@media (min-width: 23.438em ) { /* iphone 6 */
						padding: 6px 12px;
						width: 160px;
					}

					@media (min-width: $screen-sm-min) {
						width: 264px;
						font-size: 1em;
					}

					@media (min-width: $screen-md-min) {
						width: 320px;
						font-size: 1em;
					}

					+ span + .input-group-btn {
						.btn {
							background-color: $bg-form-control;
							border-radius: 0 4px 4px 0;
							@include transition(all 0.2s ease-in-out);
						}
					}
				}
			}

			.input-group-btn {
				width: auto;
				float: left;
				.btn {
					height: 31px;
					padding: 0;
					width: 31px;
					background-color: transparent;

					&:hover, &:focus, &:active {
						color: $link-color;
						outline: none;
					}
				}

				.fa {
				    position: relative;
					top: -2px;
					font-size: 18px; //nedded
					line-height: 1.85em;
				}
			}
		}
	}

	&__toggle {
		position: relative;
	    padding: 10px 7px;
	    background-color: transparent;
	    background-image: none;
	    border: 1px solid transparent;
	    border-radius: 4px;
	    margin-left: -11px;
        vertical-align: middle;

	    .icon-bar {
		    display: block;
		    width: 22px;
		    height: 2px;
		    border-radius: 1px;
	        background-color: #fff;

	        & + .icon-bar {
	        	margin-top: 4px;
	        }
		}
	}

	&__more-links {
	    display: block;
	    text-align: center;
	    margin: 6px -14px 0 3px;
	    padding: 10px 15px
	}

	&__explore-button, &__itv-button {
		display: inline-block;;
		vertical-align: middle;

	    & > a {
	    	position: relative;
		    display: block;
		    padding: 15px 15px 9px;

		    [data-locked="true"] & {
		    	text-transform: uppercase;
		    }
	    }

	    .btn-disabled {
	    	cursor: not-allowed;
	    }
	}

	&__help-button.btn {
		display: block;
		line-height: 1;
		font-size: 1em;
		border: 2px solid rgba(255,255,255,0.5);
		color: rgba(255,255,255,0.5);
		margin: 10px 0 0 15px;
		padding: 5px 8px;
		border-radius: 50%;

		@media screen and (min-width: $screen-md-min) {
			margin: 19px 0 0 15px;
			padding: 0 4px;
		}

		&:hover {
			border-color:  rgba(255,255,255,0.8);
			color:  rgba(255,255,255,0.8);
		}
	}
}

.profile-avatar {
	width: 42px;
	height: 42px;
	margin-left: 10px;
	margin-bottom: 0;
	border-radius: 0;

}

.navbar-profile-link {
	padding: 0;
	line-height: 55px;
	font-size: 0.875em;
	@media (min-width: $screen-md-min) {
		padding: 0 15px;
	}
}

.navbar-profile-link, .profile-avatar {
	@media (min-width: $screen-md-min) {
		display: inline-block;
		vertical-align: middle;
	}
}

.navbar-brand {
	width: 120px;
	display: inline-block;
    vertical-align: middle;

	@media (max-width: $screen-xs-min) {
		width: 95px;
	}

	img {
		width: 100%;
		display: block;
	}
}

.navbar-landing {
	opacity: 0;
	@include transition(all 0.5s ease-in-out);
}

/* minimalist version of the nav*/
.navbar-short {
	.navbar-header {
		margin-top: 7px;
		@media( max-width: $screen-xs-min) {
			margin-top: 13px;
		}
	}

    .login-buttons  .btn {
    	@media (max-width: $screen-xs-min) {
	    	margin: 0;
	    	padding: 4px 6px;
    	}

    	@media (min-width: 23.438em) and (max-width: $screen-xs-min) {
			padding: 5px 12px;
		}

		@media (max-width: 320px) {
	    	font-size: 11px;
		}
    }
}

.navbar-visible {
	opacity: 1;
}

.navbar-fixed-top {
	position: fixed;
	top: 0;
	z-index: 99;
	right: 0;
    left: 0;

    -webkit-backface-visibility: hidden;


    &.banner {
    	top: 78px;
    }

    &.navbar-landing {
    	z-index: -1;

    	.login-buttons {
			margin-top: 6px;

			@media(max-width: $screen-xs-min) {
				margin-top: 12px;
			}
		}
    }

    &.navbar-landing.navbar-visible {
    	z-index: 99;
    }
}

/* Hide navbar on scroll down */
.nav-up {
   top: -$navbar-min-height;
}


/* open search for mobile/tablet */
.search-nav-view {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	z-index: -1;
	@include transition(opacity 0.2s ease-in-out);

	&.active {
		opacity: 1;
		z-index: 1;
	}

	.btn-back {
		max-width: 15%;
		margin: 8px 0 0;
		color: $text-color;
		&:focus, &:active {
			color: $link-color;
		}
	}

	.navbar__form {
		width: 83%;
		.input-group {
			width: 100%;
			.form-control {
				width: calc(100% - 40px);
				padding: 6px 10px;
				height: 31px;
				background-color: $bg-form-control;

				@media (min-width: 23.438em ) { /* iphone 6 */
					padding: 6px 12px;
				}

				@media (min-width: $screen-sm-min) {
					font-size: 1em;
				}

				+ .input-group-btn {
					.btn {
						background-color: $bg-form-control;
						border-radius: 0 4px 4px 0;
					}
				}
			}
		}
	}
}

.default-nav-view {

	visibility: visible;
	&.inactive {
		visibility: hidden;
	}

	h1 {
		font-family: $h1-headings-font-family;
		font-size: 24px;

		@media (max-width: $screen-sm-min) {
		    margin-top: 15px;
			margin-bottom: 10px;
			font-size: 22px;
		}
	}
}

@media only screen
  and (min-device-width: 320px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (orientation: landscape) {
  	.navbar-landing .logo-landing, .navbar_fake .logo-landing {
  		width: 100px;
  	}

}

//galaxy tablet landscape
@media
(min-device-width: 600px)
and (max-device-width: 1024px)
and (-webkit-min-device-pixel-ratio: 1)
and (orientation: landscape) {
	.navbar-landing .logo-landing, .navbar_fake .logo-landing {
  		width: 120px;
  	}
}
