.media-object {
	width: 56px;
	height: 75px;
	border: none;
	background-color: $bg-media-object;
	background-image: url($img-bg-slides);
	background-repeat: no-repeat;
	background-position: center center;
	background-size: 65%;
}

.media.payment-method {
	padding: 15px 15px 15px 0;
}

.detail {
	.media-heading {
		margin-top: 35px;
	}
}

.media-coupons {
	margin-bottom: 15px;
}

.credit-card {
	.credit-card-logo {
		background-image: url($img-payment-methods);
		background-position: -75px -350px;
		background-repeat: no-repeat;
		position: relative;
		width: 50px;
		height: 35px;

		&:after {
			content: '\f09d';
			font-family: FontAwesome;
			font-style: normal;
			font-weight: normal;
			text-decoration: inherit;
			position: absolute;
			font-size: 32px;
			line-height: 32px;

			@media(min-width: $screen-sm-min) {
				font-size: 40px;
				line-height: 40px;
			}
		}

		&.visa {
			background-position: 0 0;
			background-size: 115%;
		}

		&.mastercard {
			background-position: 0 -43px;
			background-size: 135%;
		}

		&.naranja {
			background-position: 0 -135px;
			background-size: 135%;
		}

		&.diners {
			background-position: 0 -188px;
			background-size: 120%;
		}

		&.nativa {
			background-position: 0 -148px;
			background-size: 115%;
		}

		&.amex {
			background-position: 0 -88px;
			background-size: 135%;
		}

		&.nevada {
			background-position: 0 -283px;
		}

		&.nevada,
		&.visa,
		&.mastercard,
		&.naranja,
		&.diners,
		&.nativa,
		&.amex {
			&:after {
				content: '';
			}
		}
	}

	p {
		margin-bottom: 0;
	}
}
