.item-title {
	display: inline-block;
	margin: 0;
	line-height: 1;
	padding: 0;
	text-align: center;
	text-shadow: 1px 1px 3px $item-title-text-shadow;
	width: 100%;

	@media(min-width: $screen-sm-min){
		text-align: left;
		float: left;
		width: inherit;
		margin-right: 10px;
		max-width: calc(100% - 95px);
	}
}

.title-list {
	font-size: 24px;
	text-align: center;
	margin-bottom: 20px;
}

.item-original-title {
	margin-top: 0;
	line-height: 1;
	text-shadow: 1px 1px 3px $item-title-text-shadow;
	max-width: calc(100% - 55px);

	.container-detail & {
		float: left;
	    line-height: 32px;
    	margin-right: 15px;
	}
}

.modal-header {
	.item-title, .item-original-title {
		width: 75%;
		margin: 0 auto;
		max-width: 100%;

		@media (min-width: $screen-lg-min) {
			width: 100%;
			text-align: center;
		}
	}
}

.grid-title {
    font-family: $h2-headings-font-family;
    //font-weight: inherit;
    line-height: $headings-line-height;
    margin-top: $line-height-computed;
    margin-bottom: ($line-height-computed / 2);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    small,
    .small {
        font-size: inherit;
    }
}

.info {
	h2 {
		border-bottom: 1px solid $border-title-details;
		padding-bottom: 10px;
	}
}
