
/* YouTube loading effect */

.la-anim-1 {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 9999;
	width: 100%;
	height: 3px;
	background-color: $bg-loading;
	background-color: $bg-loading;
	@include translate3d(-100%, 0, 0);
	pointer-events: none;
	visibility: hidden;
}

.la-anim-1::after {
	content: '';
	position: absolute;
	top: 0;
	right: 0;
	width: 100px;
	height: 100%;
	opacity: 0;
    background-color: $bg-loading;
	@include box-shadow(0 0 15px $shadow-loading, 0 0 5px $shadow-loading);
	@include transition(opacity 0.2s, box-shadow 2s 4s);
	@include transform(#{rotate(2deg) translateY(-2px)});
	visibility: hidden;
}

.la-anim-1.la-animate {
	z-index: 9999;
	opacity: 0;
	@include transition(transform 5s ease, opacity 1s 5s);
	@include transform(#{translate3d(0%, 0, 0)});
	visibility: visible;
}

.la-anim-1.la-animate::after {
	box-shadow: 0 0 3px $shadow-loading, 0 0 1px $shadow-loading;
	opacity: 1;
	visibility: visible;
}
