.change-group {
	@extend .form-group;
	overflow: hidden;
	border-bottom: 1px solid $change-group-border-color;
	font-size: 0.875em;
	margin: 0 0 5px;
	padding: 20px 0 0;

	@media (min-width: $screen-sm-min) {
		margin: 0 15px 5px;
		padding: 25px 0 25px 15px;
	}

	p {
		//margin-top: 10px;
	}

	.btn {
		display: inline-block;
	}

	&_alter {
		border-bottom: 1px solid $change-group-border-alter-color;
		padding: 15px 0;
	}

	&.short-container {
		padding-bottom: 20px;
	}

	&.overflow-visible {
		overflow: visible;
	}

}

.form-group {
	position: relative;

	&_alter {

		i {
			position: absolute;
		    right: 0;
		    top: 2px;
		    padding: 10px;
		    z-index: 9;

		   	[data-locked="true"] & {
		   		display: none;
		   	}
		}
	}
}

.check-coupon {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 1.563em;
    color: $check-coupon-color;
}

.active-coupon {
	color: $check-coupon-color;
}

.inactive-coupon {
	color: $inactive-coupon-color;
}

.check-input {
    position: absolute;
    top: 7px;
    right: 12px;
    font-size: 1.563em;
    color: $check-input-color;
}

.user-info-label {
	color: $user-info-label-color;

	&.suscription-label {
		font-size: 16px; //needed
	}
}

//alquileres solo mobile
.movie-item {
	@include clearfix();
	margin-top: 20px;
	padding-bottom: 10px;

	.img-responsive {
		width: 100%;
		height: auto;
	}
}

.consumption-info {
	margin-top: 7px;
	margin-bottom: 7px;
	font-size: 0.875em;
}

.consumption-btn {
	.btn {
		font-size: 0.875em;
	}
}

.helper-alignment-btn .btn {
	margin-left: -12px;
}

.js-togglable-payment-method + .js-togglable-payment-method {
	margin-top: 20px;
}

.add-coupon-special {
	float: right;
	margin-top: -34px;
}
