.webui-popover {
	width: 400px;
	z-index: 1040;
	border-radius: 0;
	min-height: 100px;
	border: 1px solid $popover-border-color;
	padding: 0;

	.webui-popover-content > div:first-child {
		width: 100%; // Fix chrome & firefox bug
	}

	.rated {
		background-repeat: no-repeat;
		background-position: 0 0;
	    width: 30px;
	    height: 19px;
	    display: inline-block;
	    margin: 0 0 -7px 0;
	    background-size: 100%;
	}

	.rated-1 { @extend .rated; background-image: url($img-incaa-clasification-atp-black-svg); }
	.rated-2 { @extend .rated; background-image: url($img-incaa-clasification-13-black-svg); }
	.rated-3 { @extend .rated; background-image: url($img-incaa-clasification-16-black-svg); }
	.rated-4 { @extend .rated; background-image: url($img-incaa-clasification-18-black-svg); }
	.rated-5 { @extend .rated; background-image: url($img-incaa-clasification-c-black-svg); }

	.quality-sd {
		@extend .rated;
		background-image: url($img-quality-sd-black);
	}

	.quality-hd {
		@extend .rated;
		background-image: url($img-quality-hd-black);
	}

	.imdb {
		background-image: url($img-icon-imdb-black);
		margin: 2px 5px 0 0;
	}

	.share {
		.dropdown-menu {
			bottom: 45px;
			left: -70px;

			&:after {
				left: 80px;
			}
		}
	}

	.dropdown-menu {
	    display: none;
	    position: absolute;
	    top: inherit;
	    border: none;
	    box-shadow: none;
	    float: left;
	}

	.open {
		.dropdown-menu {
			display: block;
		}
	}

	&.right,
	&.right-top,
	&.right-bottom {
		margin-left: 15px;
	}

	/* &.right>.webui-arrow:after, &.right>.arrow:after,
	&.right-top>.webui-arrow:after, &.right-top>.arrow:after,
	&.right-bottom>.webui-arrow:after, &.right-bottom>.arrow:after {
		left: -5px;
		bottom: -25px;
	} */

	&.right>.webui-arrow, &.right>.arrow,
	&.right-top>.webui-arrow, &.right-top>.arrow,
	&.right-bottom>.webui-arrow, &.right-bottom>.arrow {
		border-right-color: transparent;
	}

	&.right>.webui-arrow:after, &.right>.arrow:after
	&.right-top>.webui-arrow:after, &.right-top>.arrow:after,
	&.right-bottom>.webui-arrow:after, &.right-bottom>.arrow:after {
		border-right-color: $bg-popover-body;
	}

	&.left,
	&.left-top,
	&.left-bottom {
		margin-left: -5px;
	}

	/* &.left>.webui-arrow:after, &.left>.arrow:after,
	&.left-top>.webui-arrow:after, &.left-top>.arrow:after,
	&.left-bottom>.webui-arrow:after, &.left-bottom>.arrow:after {
		right: -5px;
		bottom: -25px;
	} */

	&.left>.webui-arrow, &.left>.arrow,
	&.left-top>.webui-arrow, &.left-top>.arrow,
	&.left-bottom>.webui-arrow, &.left-bottom>.arrow {
		border-left-color: transparent;
	}

	&.left>.webui-arrow:after, &.left>.arrow:after,
	&.left-top>.webui-arrow:after, &.left-top>.arrow:after,
	&.left-bottom>.webui-arrow:after, &.left-bottom>.arrow:after {
		border-left-color: $bg-popover-body;
	}

	.webui-arrow {
		&:after {
			border-width: 25px;
		}
	}

	.webui-popover-content {
		padding: 0;

		p {
			font-family: $font-family-base;
		}

		.webui-popover-header {
			overflow: hidden;
			background-color: $bg-popover-header;
			display: table;
			width: 100%;
			min-height: 55px;
			padding: 0 15px;

			.item-title {
				margin-top: 0;
				font-size: 1.063em;
				font-family: $popover-item-title;
				padding: 5px 0;
				line-height: 1.3;
				display: table-cell;
				vertical-align: middle;
				float: none;

				b {
					font-family: $font-family-base;
				}
			}

			.item-original-title {
				font-size: 0.875em;
				font-family: $font-family-base;
			}

			.rating {
				font-size: 0.875em;
			}
		}

		.webui-popover-body	{
			overflow: hidden;
			background-color: $bg-popover-body;
			color: $color-popover;
			padding: 10px 0;

			.btn-link {
				margin-top: 8px;
			}
		}

	}
}

.webui-popover-inverse {

	background-color: $bg-popover-body;

	&.right>.webui-arrow, &.right>.arrow,
	&.right-top>.webui-arrow, &.right-top>.arrow,
	&.right-bottom>.webui-arrow, &.right-bottom>.arrow {
		border-right-color: transparent;
	}

	&.right>.webui-arrow:after, &.right>.arrow:after,
	&.right-top>.webui-arrow:after, &.right-top>.arrow:after,
	&.right-bottom>.webui-arrow:after, &.right-bottom>.arrow:after {
		border-right-color: $bg-popover-body;
	}

	&.left>.webui-arrow, &.left>.arrow,
	&.left-top>.webui-arrow, &.left-top>.arrow,
	&.left-bottom>.webui-arrow, &.left-bottom>.arrow {
		border-left-color: transparent;
	}

	&.left>.webui-arrow:after, &.left>.arrow:after,
	&.left-top>.webui-arrow:after, &.left-top>.arrow:after,
	&.left-bottom>.webui-arrow:after, &.left-bottom>.arrow:after {
		border-left-color: $bg-popover-body;
	}
}

.webui-popover i.icon-refresh {
  background: url($img-loading) no-repeat;
  top: 40%;
}
