.header-user-mobile {
    a.btn-back {
        display: inline-block;
        vertical-align: middle;
        color: $btn-back-color;

        &:hover,
        &:focus,
        &:active,
        &:hover:active {
            color: $link-hover-color;
        }
    }

    &__title {
        display: inline-block;
        vertical-align: middle;
        margin-top: 10px;
    }

    &__search {
        margin-right: 10px;
    }
}