.media-actions {
	margin-top: -30px;
	margin-bottom: 20px;
	position: relative;
	background-color: lighten($bg-media,5%);
	padding: 10px;
	float: left;
	width: 100%;

	@media (min-width: $screen-md-min) {
		background-color: $bg-media;
		margin-top: 0;
	}

	.progress {
		position: absolute;
		top: 0;
		width: 100%;
		left: 0;
	}

	.stars {
		padding: 6px 10px;

		a {
			color: $text-color;
			padding: 0 3px;

			&:hover {
				color: $stars-hover-bg;
			}
		}
	}
}
