.fa-stack {
	@include alpha-attribute('background-color', rgba($link-color, 0.8), $light);
	border-radius: 100%;
	height: 3em;
	width: 3em;
	line-height: 1em;

	&.no-bg {
		background-color: transparent;
	}

	.fa-play {
	    position: absolute;
		left: 55%;
	    font-size: 1.875em;
	    top: 50%;
	    @include translate(-50%, -50%);
	}

	@media (min-width: $screen-md-min) {
	 	height: 4em;
		width: 4em;
		line-height: 2em;
	 }
}

.fa-spinner {
	font-size: 3em;
	 @media (min-width: $screen-md-min) {
	 	font-size: 4em;
	 }
}

.favourite-heart {
	color: $favourite-heart;
	background-color: transparent;

	[data-locked="true"] & {
		&:hover {
			color: darken($favourite-heart,20);
		}
	}
}

.fa-circle {
	&:before {
		@include alpha-attribute('color', rgba($icon-color, 0.8), $light);
	}
}

 .not-fa {
	background-image: url($img-add-list);
 	background-repeat: no-repeat;
    background-position: 2px 0px;
    width: 20px;
    height: 21px;
    display: block;
    background-size: 85% auto;


 	&.favourite-heart {
		background-position: 2px -23px;
	}

}

.origin-genre {
	text-shadow: 1px 1px 3px $item-title-text-shadow;
	opacity: $detail-text-opacity;
}

.rated {
	//background-image: url($img-incaa-clasification);
	background-repeat: no-repeat;
	background-position: 0 0;
    width: 23px;
    height: 19px;
    display: inline-block;
    margin: 0 0 -7px 0;

    @media(min-width: $screen-sm-min){
		width: 30px;
    }

    .additional-info-detail & {
    }
}

.rated-1 { @extend .rated; background-image: url($img-incaa-clasification-atp-white-svg); }
.rated-2 { @extend .rated; background-image: url($img-incaa-clasification-13-white-svg); }
.rated-3 { @extend .rated; background-image: url($img-incaa-clasification-16-white-svg); }
.rated-4 { @extend .rated; background-image: url($img-incaa-clasification-18-white-svg); }
.rated-5 { @extend .rated; background-image: url($img-incaa-clasification-c-white-svg); }

.quality-sd {
	@extend .rated;
	background-image: url($img-quality-sd);
}

.quality-hd {
	@extend .rated;
	background-image: url($img-quality-hd);
}

.short-summary {
	font-size: 13px;
	line-height: 1.3;
	font-weight: normal;
	margin-bottom: 15px;
}

.additional-info, .item-details-info {
	font-size: 0.750em;
	line-height: 1.4;
}

.item-details-info {
	padding-bottom: 10px;

	a, span {
		color: $link-color;
		font-family: $popover-item-title;
	}
}

.additional-info {
	overflow: hidden;
	padding-bottom: 15px;

	li {
		display: inline-flex;
		margin: 0 15px 0 0;
		float: left;

		span.icons {
			padding-left: 30px;
			background-repeat: no-repeat;
			background-position: 0 0;
			height: 19px;
			display: block;
			text-transform: uppercase;

			&.icon-clock {
				padding-left: 0px;
				text-transform: none;
			}

			&.icon-subtitled {
				background-image: url($img-icon-popover-subtitle);
			}

			&.icon-audiotrack {
				background-image: url($img-icon-popover-volume);
			}
		}
	}

	&-detail {
		opacity: $detail-text-opacity;
		margin: 15px 0;
		overflow: hidden;
		text-shadow: 1px 1px 3px $item-title-text-shadow;
		display: table;
		vertical-align: middle;

		@media (min-width: $screen-md-min){
			margin: 15px 0 55px;
		}

		li {
			//float: left;
		    //margin-right: 10px;
			display: table-cell;
			padding: 0 7px 0 0;
			font-size: 14px;

			@media(min-width: $screen-lg-min){
				padding: 0 10px 0 0;
			}
		}

		.icons {
			width: 19px;
		    height: 19px;
		    display: inline-block;
		    float: left;
		    background-repeat: no-repeat;
		    margin: 3px 3px 0 2px;

		    @media(min-width: $screen-sm-min){
				width: 28px;
		    }
		}

		.icon-subtitled {
			background-image: url($img-icon-subtitle);
		}

		.icon-audiotrack {
			background-image: url($img-icon-audiotrack);
		}

		.imdb {
			background-image: url($img-icon-imdb);
			margin: 6px 5px 0 0;
		}

	}
}

.item-play {
	.fa-play {
		//margin-right: 25px;
	}
}
