.box {
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	width: 100%;
}

.box-top {
	@extend .box;
	top: 20%;
}

.block-logos {
	overflow: hidden;
    margin: 0 auto;
    display: block;
    max-width: 360px;
}

.block-info {
	-webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    max-width: 440px;
    margin: 0 auto 30px;
}



