.paginationjs {
	.paginationjs-pages {
		ul {
			margin: 1px 0 0;

			li {
				border: none;

				a {
					cursor: pointer;
					color: $pagination-color;
					background-color: transparent;

					&:hover, &:focus {
						background-color: transparent;
						color: $pagination-hover-color;
					}
				}

				&.active {
					a {
						background-color: transparent;
						color: $pagination-hover-color;
					}
				}

				&.disabled {
					a {
						opacity: 0.7;
						cursor: not-allowed;
					}
				}

				&:last-child {
					border: none;
				}
			}
		}
	}
}