.pin-success {
	background-color: $bg-pin-success;
	color: $text-pin-success;
	border-color: $border-color-pin-success;
	padding: 15px;
}

#confirm-promotion-alert{
    .promotion-success {
        background-color: $bg-pin-success;
        color: $text-pin-success;
        border-color: $border-color-pin-success;
        margin-top: 0px;
        opacity: 0.9;
        padding: 20px 0px;
    }
}

.alert-success {
	background-color: $bg-alert-success;
	border-color: $bg-alert-success;
	color: $text-color;
	text-align: center;
	position: relative;
}

.alert-danger {
	background-color: $bg-alert-error;
	border-color: $bg-alert-error;
	color: $text-color;
	text-align: center;
	position: relative;
}

.alert-dismissible {
	.close {
		right: 0;
	}
}
