.select-user {
	list-style: none;
	padding: 0;
	margin: 0;

	li {
		margin: 3px 0;
		cursor: pointer;

		a {
			display: block;
			padding: 3px 10px;

			&:hover {
				text-decoration: none;
			}

			img, span, .fa-plus {
				display: inline-block;
			}

			span {
				max-width: 70%;
    			vertical-align: middle;
			}
		}

		&:hover {
			background-color: $link-color;
		}

		img {
			max-width: 56px;
			min-height: 56px;
			margin-right: 10px;
			background-color: $bg-user-profile;
		}

		.profile-edit {
			display: none;
		}

	}
}

.login {
	width: 100%;
	margin: 0 auto;
	overflow: hidden;
	max-width: 280px;

	@media (min-width: $screen-sm-min) {
		max-width: 50%;
		overflow: visible;
	}

	h1 {
		margin: 10px auto 30px;

		img {
			max-width: 50%;
		}

		@media (min-width: $screen-md-min) {
			max-width: 50%;
			margin: 0 auto 50px;

			img {
				max-width: none;

				.device-ios & {
					max-width: 100%;
				}
			}
		}
	}

	.select-user {
		width: auto;
	    margin-left: -22px;

	    @media (min-width: $screen-md-min) {
			margin-left: 28px;
		}

		li {
			cursor: auto;
	        width: 100%;
	        max-width: 95px;
	        min-width: 95px;
    		font-size: 0.875em;
		    position: relative;
		    margin-bottom: 38px;
		    margin-left: 38px;
	        background-clip: content-box;
	        float: left;
	        min-height: 155px;

	        @media (min-width: $screen-sm-min) {
				max-width: 144px;
	        	min-width: 144px;
	        	min-height: 185px;
	        }

			&:hover {
				background-color: transparent;
			}

			a {
				color: $text-color;
			    padding: 0;
			    text-align: center;
			    display: block;
			    vertical-align: top;
			    margin: 0;
		    	word-break: break-all;
		    	@include transition(all .2s ease-in-out);

				&:hover {
					color: $link-color;
				}
			}

			span {
				width: 100%;
				max-width: none;
				text-align: left;
				padding-right: 20px;
				vertical-align: inherit;
				margin-top: 2px;
			}

			img {
				max-width: 100%;
				min-height: inherit;
				width: 100%;
				border: 0;
				display: block;
				margin-right: 0;

				&:hover {
					border: 2px solid $border-avatar;
				}
			}

			img, .fa-plus {
				border: 2px solid $bg-user-profile;
				@include transition(all .2s ease-in-out);
			}

			span {
				width: 100%;
			}

			.fa-plus {
					padding: 0;
			    position: absolute;
			    top: 50%;
			    border: 0;
			    left: 50%;
					@include translate(-50%, -50%);
			}

			.add-profile-block {
				background-color: $bg-user-profile;
				height: 96px;
				width: 96px;
				position: relative;
				@include transition(all .2s ease-in-out);

				@media (min-width: $screen-sm-min) {
					height: 144px;
					width: 144px;
		        }

				& + span {
					padding: 0;
				}

				&:hover {
					border: 2px solid $border-avatar;
				}
			}


			#create-profile-image-button {
				display: block;
			}


			&:hover {
				.profile-edit {
					display: block;
				}
			}
		}

		.profile-edit {
			position: absolute;
			bottom: 37px;
			right: 0px;
			width: 20px;
			height: 20px;
			@media (min-width: $screen-sm-min) {
				bottom: 13px;
			}
		}

		.fa-pencil {
			color: $text-color;
		}
	}
}

[data-locked="true"] .login {

	.select-user {

		li {
			cursor: auto;
			display: inline-block;
		    width: auto;
		    position: relative;
		    padding: 0;
		    margin: 0;
		    vertical-align: top;
		    float: none;
		    min-width: inherit;
    		max-width: inherit;

			&:hover {
				background-color: transparent;
			}

			a {
				color: $text-color;
			    padding: 0;
			    text-align: center;
			    display: block;
			    vertical-align: top;
			    margin: 0;
		    	max-width: 96px;
		    	word-break: break-all;

				&:hover {
					color: $link-color;
				}
			}

			span {
				width: auto;
				text-align: center;
				padding: 0;
				vertical-align: inherit;
			}

			img {
				max-width: 96px;
				min-height: 96px;
				width: 100%;
				border: 0;
				display: block;
				margin-right: 0;

				&:hover {
					border: 2px solid $border-avatar;
				}
			}

			.add-profile-block {
				background-color: $bg-user-profile;
				height: 96px;
				width: 96px;
				position: relative;
				@include transition(all .2s ease-in-out);

				&:hover {
					border: 2px solid $border-avatar;
				}
			}

			.fa-plus {
				padding: 0;
			    position: absolute;
			    top: 50%;
			    border: 0;
			    left: 50%;
				@include translate(-50%, -50%);
			}

			#create-profile-image-button {
				display: block;
			}

			& + li {
				margin: 0 0 0 38px;
			}

			&:hover {
				.profile-edit {
					display: block;
				}
			}
		}

		.profile-edit {
			position: absolute;
			top: 5px;
			right: 5px;
			width: 20px;
			height: 20px;
			background: rgba(44,44,56,0.8);
			display: none;

			&:hover {
				background-color: $link-hover-color;
			}

			&:focus {
				background-color: $link-focus-color;
			}
		}

		.fa-pencil {
			color: $text-color;
		}
	}
}
