.share {
	float: left;

	.dropdown-menu {
		background-color: lighten($bg-media,10);
		bottom: 35px;

		.container-detail &{
			top: 35px;
			right: 0;
			bottom: inherit;
    		left: inherit;

			@media(min-width: $screen-md-min){
				left: inherit;
				right: inherit;
			}
		}

		li {
			a {
				color: $text-color;

				&:hover {
					background-color: lighten($bg-media,15);
				}
			}
		}

		&:before {
			content: '';
			display: none;
		}

		&:after {
			content: '';
			width: 0;
			height: 0;
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			border-top: 10px solid lighten($bg-media,10);
			position: absolute;
			bottom: -10px;
			left: 10px;

			.container-detail & {
			    left: 40px;
			    display: none;
			}
		}
	}
}
