// Button variants
//
// Easily pump out default styles, as well as :hover, :focus, :active,
// and disabled options for all buttons

@mixin button-variant($color, $background, $border, $backgroundHover, $backgroundActive) {
  color: $color;
  background-color: $background;
  border-color: $border;

  &:focus,
  &.focus {
    color: $color;
    background-color: $backgroundHover;
    border-color: $backgroundHover;
  }

  &:hover {
    color: $color;
    background-color: $backgroundHover;
    border-color: $backgroundHover;
  }

  &:active,
  &.active,
  .open > &.dropdown-toggle {
    color: $color;
    background-color: $backgroundActive;
    border-color: $backgroundActive;

    &:hover,
    &:focus,
    &.focus {
      color: $color;
      background-color: $backgroundActive;
      border-color: $backgroundActive;
    }
  }

  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }


  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: $background;
          border-color: $border;
    }
  }

  .badge {
    color: $background;
    background-color: $color;
  }
}

@mixin link-variant($color, $background, $colorHover, $colorActive, $font, $colorDisabled) {
  color: $color;
  background-color: $background;
  border-radius: 0;
  font-family: $font;
  @include transition(color .3s linear);

  &:hover {
    color: $colorHover;
    @include box-shadow(none);
    text-decoration: none;
  }

  &:hover, &:focus {
    color: $colorHover;
  }

  &:active  { 
    color: $colorActive;
    @include box-shadow(none);
  }

  &:active,
  &.active,
  .open > &.dropdown-toggle {
    background-image: none;
  }


  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      color: $colorDisabled;
    }
  }
}

// Button sizes
@mixin button-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius) {
  padding: $padding-vertical $padding-horizontal;
  font-size: $font-size;
  line-height: $line-height;
  border-radius: $border-radius;
}
