.bg-full {
	background-repeat: no-repeat;
	background-position: center top;
	-webkit-background-size: cover;
	-moz-background-size: cover;
	-o-background-size: cover;
	background-size: cover;
	background-color: transparent;
	background-attachment: fixed;
	-ms-overflow-style: none;

	&:after {
		content: "";
		background-color: $bg-full;
		opacity: 0.85;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		position: fixed;
		z-index: -1;
		-ms-overflow-style: none;
	}
}

.bg-user {
	background-color: $bg-user;
}

@each $name, $background in $backgrounds {
	.img-bg-full-#{$name} {
		background-image: url($background);
	}
}

.bg-error {
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	background-color: $bg-body;
	background-attachment: fixed;

	@media(max-width: $screen-sm){
		background-image: none;
		background-color: $midnight;
	}

	@media(max-width: $screen-lg) and (orientation: landscape){

		.container-error .container-absolute {
			height: auto;
		}

		.container-error h2 {
			padding-right: 0;
    		text-align: center;
		}

		.container-error .btn-lg {
			display: block;
    		margin: 0 auto;
    		width: 240px;
		}

		.container-error img {
			display: block;
			width: 90%;
			margin: 0 auto;
		}
	}

}

.bg-error-500 {
	background-repeat: no-repeat;
	background-position: center top;
	background-size: cover;
	background-attachment: fixed;
	background-image: url($img-bg-err-500);

	.btn-outline {
		margin-bottom: 70px;
	}
}

// Detail

.block-benefits-subscribe {
	display: block;
	clear: both;

	@media(min-width: $screen-sm-min){
		@include alpha-attribute('background-color', rgba($white, 0.1), $dark);
	    padding: 5px 120px 20px;
    	border-radius: 5px;
	}

}
