.modal-trailer{
    position: absolute;
    min-height: inherit;
    height: 100%;
    width: 100%;
    top: 0px;
    left: 0px;

    .trailer-container{
        position: relative !important;
        width: 100% !important;
        min-height: 525px;

        @media (min-width: $screen-sm-min) {
            min-height: 675px;
        }

        .vjs-thumbnail-holder {
            display: none;
        }
    }

    .close.js-close-trailer {
        position: absolute;
        top: 45px;
        right: 45px;
        z-index: 999;

        .fa-times-circle {
            color: $text-color;
        }
    }

    .modal-dialog {
        @media (min-width: $screen-sm-min) {
            width: 700px;
        }
        
        @media (min-width: $screen-md-min) {
            width: 900px;
        }

        @media (min-width: $screen-lg-min) {
            width: 1200px;
        }
    }

    .modal-error {
        width: 300px;

        .trailer-container {
            min-height: 150px;
            text-align: center;
        }

        @media (min-width: $screen-sm-min) {
            width: 600px;
        }
    }
}
