.container {
	@include container-fixed;
	width: 100%;

	// tablet, mostly we dont use this but for specific details
	@media (min-width: $screen-tablet) {
		width: $container-sm;
	}
	// medium devices
	@media (min-width: $screen-desktop) {
		width: $container-md;
	}
	// Desktop
	@media (min-width: $screen-lg-desktop) {
		width: $container-lg;
	}
	// Large desktop
	@media (min-width: $screen-xlg-desktop) {
		width: $container-xlg;
	}
}

.container-fluid {
	@include container-fixed;
	width: 100%;
}

footer {
	@media (min-width: $screen-md-min) {
		.container-fluid {
			padding-left: 20px;
			padding-right: 20px;
		}
	}
}

/* Extends bootstrap containers */

.container-absolute {

	[data-locked="true"]  & {
		@extend .container;
		position: absolute;
		top: 50%;
		left: 50%;
		@include translate(-50%,-50%);
	}
	@media (min-width: 37.500em){
		@include container-fixed;
		width: 100%;
		position: absolute;
		top: 50%;
		left: 50%;
		@include  translate(-50%,-50%);
	}
}

.container-full {
	@extend .container-fluid;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	margin: 0 auto;
	margin-top: - $navbar-height;
}

.container-error {
	@extend .container-fluid;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	margin: 0 auto;
	text-align: center;

	@media (max-width: $screen-xs-min) {
		padding-top: 70px;

		.btn-lg {
			width: 80%;
		}
	}

	h2 {
		font-size: 33px;
		margin: 35px 0 20px;
    	padding-right: 118px;

    	@media (max-width: $screen-md-min) {
			padding-right: 0px;
    	}

    	@media (max-width: $screen-xs-min) {
			padding: 0 20px;
			text-align: center;
			font-size: 26px;
    		margin-bottom: 30px;
    	}
	}

	@media (min-width: $screen-md-min) {
		text-align: left;

		.btn-lg {
			padding: 10px 78px;
		}
	}

	h2.quote {
		text-align: left;
		margin-top: 100px;

		@media (min-width: $screen-md-min) {
			text-align: right;
			margin-top: 0;
		}
	}

	h4.quote-author {
		text-align: right;
	}

	p.quote-film {
		text-align: right;
		color: $quote-film-color;
		font-weight: bold;
	}

	.container-absolute {
		height: 50%;
	}
}

.container-wrapper {
	@extend .container;
	-webkit-box-flex: 1;
    -webkit-flex: 1 0 auto;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
	position: relative;
}

.container-detail {
	@extend .container;
	padding-bottom: 35px;
	position: relative;

	.detail & {
		margin-top: -50px;

		@media (min-width: $screen-sm-min) {
			margin-top: -500px;
		}
	}
}

.container-detail-mobile {
	@extend .container-detail;
	margin-top: -$navbar-min-height;
}

.container-search {
	@extend .container-wrapper;
	margin-top: -$navbar-height;
	padding-top: $navbar-height;
	background-color: $bg-body;
	width: 100%;

	@media (min-width: $screen-sm-min) {
		padding-top: $navbar-height * 2;
	}
	
	.block-group-filter {
		text-align: center;

		.btn-group-filter {
			display: inline-block;
			margin: 20px auto;

			@media(min-width: $screen-sm-min){
				margin: 0 auto;
			}
		}
	}

	a.tag {
		margin: 0 15px 0 0;
	}
}

.container-grid {
	@extend .container-fluid;
	background-color: $bg-body;
}

.container-landing {
	@extend .container-fluid;
	padding: 0;
	margin-top: -$navbar-min-height;
	overflow-x: hidden;
    overflow-y: auto;
}

@media (min-width: $screen-xlg-min) {
	@include make-grid(xlg);
}

@media (min-width: $screen-mxlg-min) {
	@include make-grid(mxlg);
}

.search-loader {
	width: 100%;
	height: 100%;
	background-color: transparent;
	opacity: 0.8;
	z-index: 999;
	top: 0;
	left: 0;
	padding: 15px 0;
}

.short-container {
	padding-bottom: 63px;
}

.deeplink-container {

	min-height: 520px;
	margin-top: -$navbar-min-height;

	.container-full {
		background-image: url($img-bg-deeplinking);
		background-repeat: no-repeat;
		background-size: cover;
	}

	h2 {
		text-shadow: 0px 3px 4px rgba($black,0.5);
		margin-bottom: 25px;
	}

	h2 + .col-xs-12 img { margin-bottom: 25px;}

	a { color: $deep-linking-color; }
}

