.avatar-container {
	position: absolute;
	padding: 15px 0;
	left: 5px;
    background-color: rgba(255,255,255,0.2);
    border-radius: 10px;
    z-index: 9;
    @include box-shadow(0px 0px 10px $bg-body);

    @media (min-width: $screen-md-min) {
    	background-color: $bg-body;
    	width: 500px;
    }

    input[type="radio"] {
		position: absolute;
		opacity: 0;
	}
	
    span {
    	width: 60px;
	    height: 60px;
	    display: block;
	    cursor: pointer;

	    img {
	    	width: 100%;
	    }
    }

    &:before {
    	content: '';
    	position: absolute;
    	top: 35px;
    	left: -15px;
    	width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-bottom: 10px solid transparent;
		border-right: 15px solid rgba(255,255,255,0.2);

		 @media (min-width: $screen-md-min) {
	    	border-right: 15px solid $bg-body;
	    }
    }

    [data-locked="true"] & {
    	width: 500px;

    	&:before { 
    		border-right: 15px solid $bg-body;
    	}
    }
}