.rating {
	unicode-bidi: bidi-override;
	direction: ltr;
	font-size: 1.250em;
}
.rating span.star {
	font-family: FontAwesome;
	font-weight: normal;
	font-style: normal;
	display: inline-block;
}

.rating span.star:before {
	content: "\f005";
	color: $text-color;
}

.rating span.star.active:before,
.rating span.star.active ~ span.star.active:before{
	content: "\f005";
	color: $rating-color;
}