.btn {
	border-radius: 25px;
	font-family: $btn-font-base;
	@include transition(all .2s ease-in-out);
	font-size: 1em;

	&:focus {
		outline: none;
		text-decoration: none !important;
	}

	&.disabled,
	&[disabled],
	fieldset[disabled] & {
		cursor: $cursor-disabled;
		@include opacity(.65);
		@include box-shadow(none);
		border: 0;
	}
}

.btn-default {
  @include button-variant($btn-default-text-color, $btn-default-bg-color, $btn-default-border-color, $btn-default-hover-color, $btn-default-active-color);
}

.btn-primary {
	//text-transform: uppercase;
  	@include button-variant($btn-primary-text-color, $btn-primary-bg-color, $btn-primary-border-color,  $btn-primary-hover-color, $btn-primary-active-color);
}

.btn-success {
  @include button-variant($btn-success-text-color, $btn-success-bg-color, $btn-success-border-color,  $btn-success-hover-color,  $btn-success-active-color);
}

.btn-info {
  @include button-variant($btn-info-text-color, $btn-info-bg-color, $btn-info-border-color,  $btn-info-hover-color,  $btn-info-active-color);
}

.btn-warning {
  @include button-variant($btn-warning-text-color, $btn-warning-bg-color, $btn-warning-border-color,  $btn-warning-hover-color,  $btn-warning-active-color);
}

.btn-danger {
  @include button-variant($btn-danger-text-color, $btn-danger-bg-color, $btn-danger-border-color,  $btn-danger-hover-color,  $btn-danger-active-color);
}

/* Button no bg with borders */
.btn-default-outline {
	@include button-variant($btn-default-outline-text-color, $btn-default-outline-bg-color, $btn-default-outline-border-color, $btn-default-outline-hover-color, $btn-default-outline-active-color);
}

/*ejemplo: ver todos */
.btn-link {
	@include link-variant($link-color, transparent, $link-hover-color, $link-focus-color, $btn-font-base, $btn-link-disabled-text-color);

	&.btn-edit {
		color: $btn-edit-color;
		font-size: 1.125em;
	}

	.container-detail & {
	    z-index: 1;
	    position: relative;
	    text-align: center;
	    display: block;
		margin: 0 auto;
	}

	.container-user & {
		padding: 0 12px;
	}
}

/*ejemplo: links del footer */
.btn-link_alter {
	@include link-variant($text-color, transparent, $link-hover-color, $link-focus-color, $btn-font-base, $btn-link-disabled-text-color);
}

/*ejemplo: boton de ingresar */
.btn-link_primary {
	//text-transform: uppercase;
	@include link-variant($text-color, transparent, $link-hover-color, $link-focus-color, $btn-font-base, $btn-link-disabled-text-color);
}

/* Button no bg and borders, border on hover  */
.btn-outline {
	@extend .btn;

	&,
	&:active,
	&.active {
		&:focus,
		&.focus {
			@include tab-focus;
		}
	}

	&:hover,
	&:focus,
	&.focus {
		color: darken($btn-default-outline-text-color,10%);
		text-decoration: none;
		border-color: darken($btn-default-outline-border-color,10%);
	}

	&:active,
	&.active {
		outline: 0;
		background-image: none;
		@include box-shadow(inset 0 3px 5px rgba(0,0,0,.125));
	}

	&.disabled,
	&[disabled],
	fieldset[disabled] & {
		cursor: $cursor-disabled;
		@include opacity(.65);
		@include box-shadow(none);
	}
}



.btn-modal {
	position: absolute;
    top: -20%;
    left: 7%;
    width: 86% !important;
    border-radius: 50px;
    font-size: 1em;

    @media screen and (min-width: $screen-md-min) {
    	font-size: 1.125em;
    }

}

.btn-register {
	@extend .btn-primary;
	margin: 7px 5px;
	padding: 5px 30px;

	&.btn-free-days {
		margin: 11px 5px;
	}
}

.btn-login {
	margin: 7px 5px;
	padding: 5px 30px ;
}

.btn-lg {
    width: 100%;
    font-size: 1em;

    //es el lg pero en mobile se necesita mas titito
    &_unfixed-width {
    	width: 49%;
    }

    @media (min-width: $screen-sm-min) {
        padding: 10px 50px;
        width: auto;

         &_unfixed-width {
	    	width: auto;
	    }
    }

    @media (min-width: $screen-md-min) {
    	font-size: 1.125em;
    }

    &.btn-block {
    	@media (min-width: $screen-sm-min) {
    		width: 100%;
    		padding: 10px 0;
    	}
    }
}

.btn-medium-center {

	@media(max-width: $screen-xs-min){
		max-width: 300px;
	}

	max-width: 250px;
	display: block;
	margin: 0 auto;
}

.btn-small {
	font-size: 0.875em;
}


.btn-rounded {
	background-color: $btn-rounded-bg-color;
	border-radius: 50%;
	padding: 6px 7px;
	margin: 0 3px;
	color: $btn-rounded-color;

	&:hover, &:focus {
		color: $btn-rounded-color;
		background-color: $link-hover-color;
	}

	&:active {
		color: $btn-rounded-color;
		background-color: $link-focus-color;
	}

	&.btn-bg-color {
		background-color: darken($btn-rounded-bg-color,20%);
	}
}

.btn.item-play {
	opacity: 1 !important;

	&_fixed-width {
		width: 12.731em;
	}

	.container-detail & {
		margin: 15px 0;

		@media (min-width: $screen-xs-min) {
			margin-top: 95px;
		}

		@media (min-width: $screen-xs-min) {
		    //max-width: 290px;
		    margin: 25px auto 30px;
    	}

		@media (min-width: $screen-sm-min) {
		    //max-width: 340px;
    		margin: 30px auto 35px;
    	}

    	@media (min-width: $screen-md-min) {
    		margin: 30px 0;
    	}
	}
}

/// class to be used extended in dropdown menus links
.menu-link-btn {
	font-family: $btn-font-base;
	padding: 15px 20px 15px 51px;

	&:hover, &:focus, &:active {
		color: $btn-menu-link-hover-color;
		background-color: $btn-menu-link-hover-bg;
	}
}

.btn-view-more {

}

.btn-ver-resumen {
	font-size: 0.875em;
	padding-left: 0;
}

//normal text link with arrow pointing down/up
.btn-link-toggable {
	padding: 0;
	margin-bottom: 20px;

	&:after {
	  	content: '\f0d7' !important;
	  	font-family: 'FontAwesome';
	  	color:  $btn-primary-bg-color;
	    margin: -2px 0 0 11px;
	    font-size: 26px;
	    line-height: 1;
	    float: right;
	  	@include rotate(180);
	}

	&.collapsed:after {
		@include rotate(0);
	}
}
// No se recomienda bajo ningún punto de vista hacer esto
a[data-toggle='tooltip']{
	outline: none;
}

.btn-group-filter {
	display: none;

	.disabled {
        cursor: not-allowed;
        background-color: $btn-group-filter-disabled;
    }

	.btn {
		@include alpha-attribute('background-color', rgba($white, 0.3), $light);
		border-radius: 0;
		color: $white;
		display: block;
		float: left;
		width: 70px;
		font-size: 10px;
		height: 30px;

		@media (min-width: $screen-sm-min) {
			width: 120px;
			font-size: 13px;
			height: 35px;
		}
	}

	.borde-left {
		border-left: 1px solid #687480;
	}

	.border-filter-left {
		border-radius: 10px 0px 0px 10px;
		-moz-border-radius: 10px 0px 0px 10px;
		-webkit-border-radius: 10px 0px 0px 10px;
		border: 0px solid #000000;
	}
	.border-filter-right {
		border-radius: 0px 10px 10px 0px;
		-moz-border-radius: 0px 10px 10px 0px;
		-webkit-border-radius: 0px 10px 10px 0px;
		border: 0px solid #000000;
		border-left: 1px solid #687480;
	}

	.show {
		background-color: $link-color;
		color: $text-color;
	}
}

.btn-default-filer {
	@extend .btn-default;
	background-color: #4d5b69;
	border: transparent;
}
