.panel {
	padding-left: 15px;
    padding-bottom: 10px;
    padding-top: 10px;
    margin-bottom: 0px;
	border: 0;
	border-radius: 0;
	box-shadow: none;
	background-color: transparent;
	overflow: hidden;

	&.content-carousel, &.channel-carousel {
		padding-left: 4px;
		@media (min-width: $screen-sm-min) {
			padding-left: 15px;
		}
	}

	&.channel-carousel {
		background-color: $bg-channel-carousel;
	}

	h1 {
		//font-family: $raleway-regular-italic;
		//font-size: 24px;

		@media (min-width: $screen-sm-min) {
		    margin-top: 20px;
			margin-bottom: 10px;
		}
	}

	h2 {
		margin: 0;

		@media (min-width: $screen-sm-min) {
		    margin-top: 20px;
			margin-bottom: 10px;
		}
	}

	.container-detail & {
		padding: 0;

		+ .panel {
			margin-top: 0;
		}
	}
}

.panel-group {

	.container-detail & {
		margin-bottom: 0;
	}
}

.panel-default {
	@media (min-width: $screen-md-min)  {
		min-height: 300px;
	}

}

.panel-success {
	.panel-heading {
		padding: 0;
		background-color: transparent;
		color: $text-color;
		border-bottom: 0;

		h2 {
			margin-top: 0;
		}
	}

	.panel-body {
		padding: 0;
	}
}

.panel-related {
	background-color: $bg-panel-related;
    position: relative;
    margin: 0;
    padding: 10px 0 0 15px;
    overflow: hidden;

	[data-locked="true"] & {
		min-width: $screen-md-min;
	}
}

.panel-info {
	padding: 15px 0;

	.label-default {
		color: $text-color;

		&:hover {
			color: $text-color;
		}
	}
}

.panel-plan {
	margin: 0 auto 15px;
	background-color: $bg-panel-plan;
	border: 2px solid transparent;
	border-radius: 4px;
	color: $bg-body;
	cursor: pointer;
	padding: 30px 15px;
	width: 95%;
	@include transition(all 0.2s ease-in-out);

	span {
		display: inline-block;
		vertical-align: middle;
		cursor: pointer;
		margin-bottom: 0;
	}

	h3 {
		margin-top: 0;
	}

	&.active {
		border-color: $border-panel-plan;
	}

	[data-locked="true"] & {
		padding: 30px 0;

		span {
			padding: 0 15px;
			display: block;
		}


		&:hover {
			@include scale(1.05);
		}

	}

}


.panel-plan--price {
	font-size: $font-size-base;
	font-weight: normal;
	color: $price-color;

	@media (min-width: $screen-sm-min) {
		font-size: $font-size-base * 1.5;
	}

	@media (min-width: $screen-md-min) {
		font-size: $font-size-base * 2.5;
	}
}

.panel-plan--name {
	max-width: 107px;

	@media (min-width: $screen-sm-min) {
		max-width: none;
	}

	h3 {
		margin-bottom: 0;
	}
}

.panel-coupon {
	padding: 10px 10px 25px;
    background-color: $panel-coupon-bg;
    border-radius: 10px;
}

.panel-credit-card-info {
	padding: 20px;
	border-radius: 10px;
	background-color: $bg-panel-credit-card-info;
}

.panel-resumen {
	padding: 0;
	background-color: $panel-resumen-bg;
	border-radius: 10px;

	h3 {
		margin-top: 0;
	}

	.panel-body {
		background-color: darken($panel-resumen-bg,5%);
	}

	.panel-plan--price {
		color: $text-color;
	}

	&_mobile {
		border-radius: 0;
		margin: 10px -30px 0;
		text-align: left;

		.panel-body {
			background-color: $panel-resumen-bg;
		}

		.panel-heading p {
			margin-bottom: 0;

			&:first-child {
				margin: 0;
			}
		}
	}
}

/*se usa en la nav left de mobile*/
.panel-group .panel.panel-unstyled {
	padding: 0;
	background-color: transparent;
	border-radius: 0;

	& > .panel-heading {
		border-radius:0;
		@extend .menu-link-btn !optional;
		padding: 0;
		@include transition(background-color 0.3s ease-in-out);
	}

	.panel-title  {
		font-size: 0.875em;
		text-align: left;
		display: block;
		padding: 12px 15px;
		font-family: $font-family-base;
	}

	a {
		@extend .menu-link-btn !optional;
		font-size: 0.875em;

		&.so {
			padding: 15px 20px 15px 0px;
		}

		&.btn-disabled {
		    background: transparent;
		    color: $btn-link-disabled-text-color;

		    .label {

		    	 &:hover {
	                color: $btn-link-disabled-text-color;
	            }
		    }
		}
	}
}


/*el que se usa en centro de ayuda mobile */
.panel-group .panel.panel-blue {
	border-top: 1px solid $panel-blue-border-color;

	& + .panel {
		margin: 0;
	}

	.js-menu-nav {
		background-color: $bg-dropdown;
		position: relative;
	}

	.js-menu-nav.collapsed {
		background-color: transparent;
	}

	.icon-flip {
        position: absolute;
        top: 17px;
        right: 25px;
    }

    .panel-title {
    	font-size: 1.250em;
    	font-family:  $panel-title;
    	font-weight: inherit;

    	&.collapsed .icon-flip {
			@include rotate(90);
		}

		.icon-flip {
			@include rotate(0);
		}
    }

    .panel-collapse {
    	font-size: 0.875em;

		.panel-title {
			padding: 12px 0px;

			@media(min-width: $screen-md){
				padding: 12px 15px;
			}

			a {
				padding: 15px 20px 15px 15px;
				display: block;
			}
		}

		p {
			a {
				padding: 0;
			}
		}

		.fade {
			opacity: 1;
		}

		.panel {
    		border-radius: 0;
    		padding: 0;
    	}

    	.panel-heading {
    		padding: 0;
    	}

    }

    .tab-content {
    	margin: 0;

    	.panel {
    		border-radius: 0;
    		padding: 0;
    		margin: 0;

    		a:not(.panel-title) {
    			padding: 0;
    			font-size: 1em;
    		}
    	}

    	.panel-heading {
    		padding: 10px 0;
    		border-radius: 0
    	}

    	.panel-title {
    		font-size: 0.875em
    	}
    }
}

.js-toggable-view-charges {
	margin-bottom: 20px;

	table {
		border-collapse: separate;
    	border-spacing: 0px 2px;
		width: 100%;
		font-size: 0.875em;
	}

	th, td {
		padding: 8px;
		text-align: left;
	}

	th:nth-child(1){
		width: 28%;
	}

	@media screen and (min-width: $screen-md-min){
		table {
			font-size: 1em;
		}

		th:nth-child(1) {
			width: auto;
		}

		th:nth-child(3) {
			width: 57%;
		}
	}

	tr {
		background-color: rgba(255,255,255,0.2);
	}
}

.panel {
	.style-select {
		.form-control {
			height: 35px;
			background-position: 98% 15px;

			@media (min-width: $screen-md-min) {
				background-position: 98% 15px;
			}
		}
	}
}
