.subscription-cancel-image,
.subscription-cancel-poll-image ,
.subscription-cancel-success-poll-image, 
.operation-approved-image,
.operation-rejected-image {
	width: 100%;
	background-image: url($img-subscription-sprite);
	background-repeat: no-repeat;
	height: 175px;
	
}

.global-collect {
	
	.operation-rejected-image {
		background-image: url($img-subscription-global-collect);
		height: 81px;
		background-position: 50% 0;
	}
}

.subscription-cancel-image {
	background-position: 50% -350px;
}

.subscription-cancel-poll-image {
	background-position: 50% -175px;
}

.subscription-cancel-success-poll-image {
	background-position: 50% 0;
	margin-top: 50px;
}

.operation-approved-image {
	background-position: 50% top;
	margin-top: 50px;
}

.operation-rejected-image {
	background-position: 50% bottom;
	margin-top: 50px;
}

.displays {
	img {
		width: 60%;
		margin: 30px auto;
	}
}

.displays-approved {
	
	img {
		margin: 30px auto;
		display: block;
		max-width: 320px;

		@media(min-width: $screen-sm-min) {
			max-width: 100%;
		}

		@media(min-width: $screen-xlg-min) {
			max-width: 80%;
		}

		@media(min-width: $screen-xs-min) {
			max-width: 320px;
		}
	}
}

[data-locked="true"]  .login {
	width: 50%;
	margin: 0 auto 60px;

	a {
		display: block;
	}
}

.app-store-logos {
	margin-top: -15px;
}

.channel-banner {
	width: 100%;
}