.input-group {
	.form-control {
		height: 33px;
	}
}

.navbar-form {
	.input-group {
		.form-control, .input-group-btn .btn {
			height: 31px;
		}
	}
}

/* Login checkbox */

input[type="checkbox"].remember {
    display:none;
	
	+ label {
	    display:inline-block;
	    width: 250px;
	    height: 20px;
	    margin: -1px 0 0;
	    vertical-align: middle;
	    background: url($img-checkbox-sprite) left top no-repeat;
	    cursor: pointer;

	    span {
	    	padding-left: 25px;
	    	font-size: 0.875em;
	    	vertical-align: top;
	    }
	}

	&:checked + label {
	    background: url($img-checkbox-sprite) left bottom no-repeat;
	}
}

/* Terms and conditions checkbox */

input[type="checkbox"].accept-terms {
    display:none;
	
	+ label {
	    display:inline-block;
	    width: 260px;
	    height: 19px;
	    margin: -1px 0 0;
	    vertical-align: middle;
	    background: url($img-checkbox-sprite) left top no-repeat;
	    cursor: pointer;

	    & > span {
	    	font-size: 0.875em;
	    	padding-left: 25px;

	    	@media (min-width: $screen-md-min){
	    		font-size: 1em;
	    	}

			a {
				color: $accept-terms-link-color;
			}
	    }
	}

	&:checked + label {
	    background: url($img-checkbox-sprite) left bottom no-repeat;
	}
}