body.modal-open {
    overflow-y: auto !important;
    padding-right: 0 !important;
}

.modal-scrollbar-measure {
    overflow: hidden;
}

.modal {
    .label-status, [data-locked="true"] & .label-status {
        top: 50px;
        right: -4px;
    }
}

.modal-content {
    background-color: $bg-modal;
    border-color: $bg-modal;
    @include box-shadow(0px 0px 10px darken($modal-box-shadow,10));

    &.global-collect {
        padding: 20px 30px;
    }

    &.promotion {
        max-width: 440px;
        background-color: $bg-modal-promotion;
        border: none;
        margin: 0 auto;

        .input-promotion .check-coupon {
            top: auto;
            margin-top: -43px;
            right: 23px;
        }

        .input-clarin input {
            display: none;
        }

        .form-control {

            @media(min-width: 320px){
                font-size: 0.7em;
            }
             @media(min-width: $screen-xs){
                font-size: 1em;
             }
        }

        .cancel-promotion {
            text-align: center;
            display: block;
            line-height: 42px;
        }

        .clearfix {
            margin-bottom: 60px;
        }

        .btn-lg {

            @media(min-width: 320px){
                padding: 10px;
            }

            @media(min-width: $screen-xs){
                padding: 10px 70px;
            }
        }
    }

    .btn-close {
        top: 10px;
        right: 10px;
        position: absolute;
        z-index: 99999;
        padding: 3px 8px;
        font-family: $font-family-base;
    }
}

.modal-header {
    border-bottom: 0;
    // background-size: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border-radius: 4px 4px 0 0;
    min-height: 215px;
    padding: 5px;

    @media (min-width: $screen-sm-min ) {
        padding: 15px;
    }

    @media (min-width: $screen-lg-min) {
        min-height: 250px;
    }
}

.modal-body {
    position: relative;
    padding: 30px 15px 15px;
    min-height: 130px;

    .disclaimer {
        text-align: center;
    }

    p {
        font-size: 0.875em;

        strong {
            font-size: 1.4em;
            line-height: normal;
            display: block;
        }

        &.error {
            font-size: 16px;
        }
    }
}

.modal.fade .modal-dialog {
    -webkit-transform: scale(0.1);
    -moz-transform: scale(0.1);
    -ms-transform: scale(0.1);
    transform: scale(0.1);
    top: 300px;
    opacity: 0;
    -webkit-transition: all 0.3s;
    -moz-transition: all 0.3s;
    transition: all 0.3s;
}

.modal.fade.in .modal-dialog {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
    -webkit-transform: translate3d(0, -300px, 0);
    transform: translate3d(0, -300px, 0);
    opacity: 1;
}

.modal-dialog {
    @media (min-width: $screen-sm-min) {
        width: 400px;
    }

    @media (min-width: $screen-lg-min) {
        width: 600px;
    }
}


.resumen-modal {
    padding: 15px;

    .item-title {
        text-align: left;
        margin: 0;
    }
}

.flash_message {
    z-index: 999999;
    top: 120px;
    left: 0;
    right: 0;
    position: fixed;
    width: 75%;
    margin: 0 auto;
    text-align: center;
}

.coupon-modal {

    .modal-header {
        min-height: inherit;
    }

    button.close {
        color: $text-color;
        font-size: 20px;
        line-height: 14px;
        padding: 9px;
        border-radius: 100%;
        background-color: lighten($bg-form-control,5%);
        height: 30px;

        &:hover, &:focus {
            background-color: lighten($bg-form-control,10%);
        }
    }

    .modal-body {
        padding-top: 0;
        min-height: inherit;

        .form-control {
            background-color: lighten($bg-form-control,5%);
            border-radius: 10px;
        }

        button.close {
            background-color: transparent;

            &:hover, &:focus {
                background-color: transparent;
                color: $link-color;
            }
        }

        .coupon-information {
            font-size: 90%;

        }
    }

    .modal-footer {
        border-top: none;
        display: flex;
        justify-content: space-around;
    }
}

.poll-quality-modal {
    .modal-dialog {
        max-width: 440px;

        .modal-body {
            padding: 15px;

            .modal-poll-title {
                margin-top: 11px;
                font-family: $modal-poll-title;
                font-size: 22px;
            }
        }
    }

}
