.slider {
	background-image: url($img-blue-qubit-logo);
	background-position: center center;
	background-repeat: no-repeat;
	background-size: 25%;

	.slick-arrow {
		z-index: 10;
		top: 50%;
		width: 50px;
		height: 100%;
		background-image: url($img-slider-arrows);
		background-repeat: no-repeat;
		background-color: transparent;
		@include transition(all .2s ease-in-out);

		&:hover {
			background-image: url($img-slider-arrows);
			background-repeat: no-repeat;
			@include alpha-attribute('background-color', rgba($slick-arrow-hover-color, 0.4), $light);
		}
	}

	.slick-prev {
		left: 0;
		background-position: left center;

		&:before {
			content: '';
		}

		&:hover {
			background-position: left center;
		}
	}

	.slick-next {
		right: 0;
		background-position: right center;

		&:before {
			content: '';
		}

		&:hover {
			background-position: right center;
		}
	}
}

.slick-slide {
	float: left;
	display: none;
}

.slick-dotted {
	&.slick-slider {
		margin-bottom: 12px;
	}
}

.slider, .static-hero {
	overflow: hidden;
	min-height: 180px;
	background-color: lighten($bg-body,10);
	position: relative;

	@media (min-width: $screen-sm-min) {
		min-height: 360px;
	}

	@media (min-width: $screen-md-min) {
		min-height: 450px;
	}
}

.slider-detail {
	position: relative;
	z-index: -1;
	margin-top: 0;
	margin-left: auto;
	margin-right: auto;
	padding-left: 0;
	padding-right: 0;
	width: 100%;

	// medium devices
	@media (min-width: $screen-desktop) {
		width: $container-md;
	}
	// Desktop
	@media (min-width: $screen-lg-desktop) {
		width: $container-lg;
	}
	// Large desktop
	@media (min-width: $screen-xlg-desktop) {
		width: $container-xlg;
	}

	.slider {
		min-height: inherit;
		background-image: none;
		height: 215px;
		overflow: hidden;
		margin-bottom: 0;

		@media (min-width: $screen-sm-min) {
			height: 525px;
		}

		@media (min-width: $screen-md-min) {
			width: 70%;
			margin: 0 auto;
		}

		.fanart-image {
			position: relative;
			background-repeat: no-repeat;
			background-size: 100%;
			background-attachment: scroll;
			background-position: center top;

			@media (min-width: $screen-sm-min) {
				background-size: cover;
				background-attachment: fixed;
			}

			@media (min-width: $screen-md-min) {
				background-size: inherit;
				background-attachment: inherit;
			}
		}

	}

	.slide-detail {
		position: relative;

		div {
			position: absolute;
			min-height: 215px;
			background-size: cover;
			background-position: center center;
			width: 100%;

			@media (min-width: $screen-sm-min) {
				min-height: 525px;
				background-size: inherit;
			}
		}
	}

	.item-play {
		opacity: 1;
		position: absolute;
		color: lighten($link-color,10);
		top: 45%;
		left: 50%;
		z-index: 3;
		@include transition(opacity .2s ease);
		@include transform(translate(-50%, -50%));
	}

	.rent-info {
		position: absolute;
		bottom: 42px;
		left: 10px;
		z-index: 9;
		font-size: 0.875em;
	}
}

.fanart-gradient {
	z-index: 1;
	background-image: -webkit-linear-gradient(transparent 50%, $fanart-gradient 80%);
	background-image: -o-linear-gradient(transparent 50%, $fanart-gradient 80%);
	background-image: linear-gradient(transparent 50%, $fanart-gradient 80%);
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	bottom: 0;

	@media (min-width: $screen-md-min) {
		width: 70%;
		right: 0;
		margin: 0 auto;
		padding-left: 15px;
		padding-right: 15px;

		&:before, &:after {
			content: '';
			position: absolute;
			height: 100%;
			width: 10%;
		}

		&:before {
			left: 0;
			background: linear-gradient(to left, transparent 0, $fanart-gradient 50%);
		}

		&:after {
			right: 0;
			background: linear-gradient(to right, transparent 0, $fanart-gradient 50%);
		}
	}
}

.slide {
	min-height: 180px;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    position: relative;

	@media (min-width: $screen-sm-min) {
    	min-height: 360px;
    }

    @media (min-width: $screen-md-min) {
    	min-height: 450px;
    }

    .btn-play {
    	visibility: hidden;
    	position: absolute;
    	top: 50%;
    	left: 50%;
    	@include translate(-50%,-50%);
    }

    .item-info {
    	position: absolute;
    	bottom: 0px;
    	color: $text-color;
    	width: 100%;

    	@media (min-width: $screen-sm-min) {
	    	bottom: 50px;
	    }

		.slide-title {
			text-transform: uppercase;
			margin-bottom: 0;
		}

    	.slide-title, .lead {
    		text-shadow: 1px 1px 1px $bg-body;
    	}

    	.lead {
    		font-family: $font-family-base;
    	}
    }

    &:hover {
    	text-decoration: none;
    }

	.item-info {

		.slider-title-container {
			padding: 5px 25px;
			margin: 0 0 15px;
			background-color: rgba($black,0.5);
		}
	}

}

.slick-arrow {
	z-index: 10;
}

.slick-prev {
	left: 25px;
	top: 40%;
	width: inherit;
	height: inherit;

	&:before {
		content: '';
	}
}

.slick-next {
	right: 25px;
	top: 40%;
	width: inherit;
	height: inherit;

	&:before {
		content: '';
	}
}

.slick-dots {
	bottom: 35px;

	li {
		button {
			font-size: 14px;
			&:before {
				color: transparent;
				border: 1px solid $slick-dot-border-color;
				border-radius: 50%;
				font-size: 1.7em;
				opacity: 1;
			}
		}

		&.slick-active {
			button {
				&:before {
					color: $link-color;
					border-color: transparent;
					margin: 1px 0 0 -2px;
				}
			}
		}
	}
}

.slick-track {
	overflow: hidden;
}

.slick-list {
    .slick-loading & {
        background: $text-color none;
    }
}
