.catfish {
	background-color: $bg-catfish;
	position: fixed;
	bottom: 3em;
	z-index: 99;
	width: 95%;
	left: 0;
	right: 0;
	margin: 0 auto;
	border: 0 none;

	.active-area {
		overflow: hidden;
		display: table;
		width: 100%;

		> div {
			float: inherit;
			display: table-cell;
  			vertical-align: middle;
  			text-align: center;
		}

	}

	p {
		color: $text-catfish;
		margin: 0;
		text-align: right;

		@media (min-width: $screen-xs-min) {
			text-align: center;
		}

		@media (min-width: $screen-sm-min) {
			padding-top: 0;
			text-align: right;
		}

		@media (min-width: $screen-md-min) {
			text-align: center;
		}
	}

	.close {
		position: absolute;
		right: 5px;
		top: 0;
		font-size: 28px;
		z-index: 9;
		font-weight: inherit;
		color: $text-color;
	}

	.btn-default-outline {
		@media (min-width: $screen-md-min) {
			text-align: right;
		}
	}
}
