.nav > li > a:hover, .nav > li > a:focus {
	background-color: transparent;
}

.nav-tabs {
	border-bottom: 0;
}

.nav li .btn-register {
	&:hover, &:focus  {
		background-color: $btn-register-bg;
	}

	&:active:focus {
		background-color: $btn-register-bg-hover;
	}
}

.nav-tabs.nav-stacked {

	border-right: 2px solid $nav-stacked-border-color;
	margin-top: 25px;

	li {
		margin-right: -2px;

		&:hover {
			border-right: 2px solid $nav-stacked-hover-color;
		}

		a  {
			background-color: transparent;
			border: none;
			color: $nav-stacked-link-color;
			font-family: $nav-stacked-font;
			font-size: 1.675em;
			line-height: 1;
			padding: 8px 10px;
			@include transition(all .2s ease-in-out);

			&:hover, &:focus {
				background-color: transparent;
				border: none;
				color: $text-color;
				font-family: $nav-stacked-hover-font;
			}
		}

		&.active {
			border-right: 2px solid $nav-stacked-border-active-color;

			a {
				background-color: transparent;
				border: none;
				color: $text-color;
				font-family: $nav-stacked-active-font;
				
				&:hover, &:focus {
					background-color: transparent;
					border: none;
					color: $text-color;
				}
			}
		}
	}
}

.nav-tabs.nav-justified {
    padding: 40px 15px 0;

	li {
		text-align: center;
		border-bottom: 2px solid $nav-justified-border-color;
		display: table-cell;
		margin: 0 0 0 20px; 

		width: 11%;
		

		&:first-child {
			text-align: left;
			width: 7%;
		}

		&:last-child {
			text-align: right;
			width: 10%;
		}

		&.active a {
			border: 0;
			border-bottom: 2px solid $link-hover-color;
			background-color: transparent;
			color: $link-hover-color;
			margin-left: 0;
			padding-bottom: 0;
			text-shadow: 0px 1px 10px $bg-body;

			&:hover {
				border: 0;
				border-bottom: 2px solid $link-focus-color;
			}
		}

		a  {
			font-family: $nav-tabs-link-font;
			border: 0;
			border-bottom: 2px solid $nav-justified-border-color;
			display: inline-block;
			color: $text-color;
			margin-left: 0;
			margin-bottom: -2px;
			padding-bottom: 0;
			padding-left: 0;
			padding-right: 0;
			text-shadow: 0px 1px 10px $bg-body;
			text-transform: uppercase;
			font-size: 1.125em;

			@media screen and (min-width: $screen-lg-min) {
				font-size: 1.425em;
			}

			@media screen and (min-width: $screen-xlg-min) {
				font-size: 1.625em;
			}

			@include transition(color 0.3s ease-in-out, border-color 0.3s ease-in-out);


			&:active{
				border: 0;
				border-bottom: 2px solid $link-hover-color;
				background-color: transparent;
				color: $link-hover-color;
				margin-left: 0;
				padding-bottom: 0;
				text-shadow: 0px 1px 10px $bg-body;
			}

			&:hover{
				border: 0;
				border-bottom: 2px solid $link-focus-color;
				background-color: transparent;
				color: $link-focus-color;
				margin-left: 0;
				padding-bottom: 0;
				text-shadow: 0px 1px 10px $bg-body;
			}
		}
		
	}
}

.tab-content {
	margin-top: 95px;

	.tab-pane {
		.panel-group {
			.panel {
				padding: 0;

				.panel-heading {
					padding-top: 0;
					border-bottom: 1px solid $bg-body;

					.panel-title {
						font-size: 1.375em;
						line-height: 1;

						a {
							display: block;
							position: relative;
							outline: 0 none;
							padding: 0 25px 0 0;
							@include transition(all 0.2s ease-in-out);

							&:after {
								content: '\f0d8';
								font-family: 'FontAwesome';
								color: $link-color;
								position: absolute;
								top: 0;
								right: 0;
							}

							&:hover, &:focus {
								color: $link-color;
							}
						}

						a.collapsed {
							&:after {
								content: '\f0d7' !important;
								font-family: 'FontAwesome';
								color: $link-color;
								position: absolute;
								top: 0;
								right: 0;
							}
						}
					}
				}

				.panel-body {
					border-top: 0;
					border-bottom: 1px solid $bg-body;

					.panel-group {
						margin-right: -15px;
						margin-top: 15px;

						.panel {
							&:last-child {
								.panel-body {
									border-bottom: 0;
								}
							}
						}
					}
				}
			
			 + .panel {
					margin-top: 30px;
				}
			}

			ul {
				margin-bottom: 20px;
				overflow: hidden;

				li {
					display: table;
					text-align: center;
					float: left;
					font-family: $tab-font;
					font-size: 1.125em;
					width: 145px;
					height: 165px;
					margin: 0;
					background-repeat: no-repeat;
					background-image: url($img-icons-devices-sprite);

					&.smart { background-position: 8px 0; }
					&.pc { background-position: 8px -165px; }
					&.tablet { background-position: 8px -330px; }
					&.celular { background-position: 8px -495px; }
					&.blu-ray { background-position: 8px -660px; }

					span {
						display: table-cell;
						vertical-align: bottom;
					}
				}
			}
		}
	}
}