.page-header {
	margin: 0;
}

.hero, .titles, .dispositivos, .quotes, .call-to-action {
	position: relative;
	overflow: hidden;
    min-height: 100vh;

	
	[data-locked="true"] & {
		height: 1024px;
		height: 100vh;
	}


	h1, h2 {
		font-size: 2em;
		text-shadow: none;
		line-height: 1;
		//font-family: $raleway-bold;

		small {
			font-family: $font-family-base;
			color: $text-color;
			font-size: 1em;
		}

		@media (min-width: $screen-sm-min) {
			font-size: 2.5em;
			line-height: 0.9;
		}

		@media (min-width: $screen-lg-min) {
			font-size: 3.125em;
		}

		@media (min-width: $screen-xlg-min) {
			font-size: 3.500em;
			line-height: 0.9;
		}

	}

	h3 {
		font-family: $h3-headings-font-family;
		font-size: 1em;
		text-shadow: none;

		@media (min-width: $screen-md-min) {
			font-size: 1.375em;
		}

		@media (min-width: $screen-xlg-min) {
			font-size: 1.563em;
		}
	}
} 

.hero {

	h1 {
		font-size: 2em;

		br {
			display: none;
		}

		@media (min-width: $screen-sm-min){
			font-size: 2.5em;
			
			br {
				display: block;
			}
		}

		@media (min-width: $screen-lg-min){
			font-size: 4.063em;

		}

		@media (min-width: $screen-xlg-min) {
			font-size: 5.625em;
		}

		
	}

	.box {
		top: 53%;
	
		//galaxy tablet landscape
		@media only screen and
		(min-device-width: 600px) 
		and (max-device-width: 1024px)
		and (-webkit-min-device-pixel-ratio: 1)
		and (orientation: landscape) {
			top: 54%;
		}

		//ipad mini : mismos estilos que $screen-sm-min, pero se reescriben por el maldito galaxy
		// device-ios es una clase por js que se incluye en html, solo para que galaxy no tome los estilos de ipad
		@media only screen 
		and (min-device-width: 768px) 
		and (max-device-width: 1024px) 
		and (-webkit-min-device-pixel-ratio: 1) {
			top: 50%;
		}

		@media only screen and (min-width: $screen-sm-min) {
			top: 50%;
		}
	}

	ul {
		@media screen and (max-width: $screen-md-max){
			margin-top: 0;
		}

		li.featured-list-group_item {
		    height: 20px;
			padding: 0px 0 0px 40px;
			text-shadow: none;
			display: table;
			position: relative;
			margin-bottom: 15px;

			h2 {
				font-size: 0.875em;
		    	line-height: 1.1;
		    	font-family: $font-family-base;
		    	margin: 0;
		    	vertical-align: middle;
		    	display: table-cell;
			}

			&:after {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				background-repeat: no-repeat;
				background-size: cover;
				background-image: url($img-sprite-landing);
				width: 30px;
				height: 30px;
			}

			&:first-child:after {
				background-position: 0 0;
			}
			&:nth-child(2):after {
				background-position: -45px 0;
			}
			&:last-child:after {
				background-position: -88px 0;
			}

			@media (min-width: $screen-md-min) {
				background-size: inherit;
				padding: 0 0 0 65px;
				height: 62px;

				h2 {
					font-size: 1.375em;
					//line-height: 2.9;
				}

				&:after {
					width: 56px;
					height: 56px;
				}

				&:first-child:after {
					background-position: 0 0;
				}
				&:nth-child(2):after {
					background-position: -84px 0;
				}
				&:last-child:after {
					background-position: -164px 0;
				}
			}

			@media (min-width: $screen-xlg-min) {
				h2 {
					font-size: 1.175em;
				}
			}
		}
	}

	ul + .btn-primary {
		padding: 9px 30px;
		width: 100%;
		font-size: 1.125em;

		@media only screen 
		and (min-device-width: 320px) 
		and (-webkit-min-device-pixel-ratio: 2)
		and (orientation: landscape) {
			padding: 7px 30px;
			font-size: 0.875em;
		}

		/* Landscape for iphone 6 */
		@media only screen 
		and (min-device-width: 667px) 
		and (-webkit-min-device-pixel-ratio: 2)
		and (orientation: landscape) {
			padding: 10px 30px;
			font-size: 1em;
		}

		@media (min-width: $screen-sm-min) {
			width: auto;
			padding: 10px 50px;
		}
	}

	.slider-landing {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		
		.bg-slider-01 { background-image: url($img-bg-hero-landing-01-mobile);};
		.bg-slider-02 { background-image: url($img-bg-hero-landing-02-mobile);};
		.bg-slider-03 { background-image: url($img-bg-hero-landing-03-mobile);};
		.bg-slider-04 { background-image: url($img-bg-hero-landing-04-mobile);};
		.bg-slider-05 { background-image: url($img-bg-hero-landing-05-mobile);};

		div {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			min-height: 320px;
			
			background-size: cover;
			background-position: center center;
			background-repeat: no-repeat;
			z-index: -1;

			[data-locked="true"] & {
				height: 1024px;
				height: 100vh;
			}
		}
	}

	[data-locked="true"] & { 
		.slider-landing {
			.bg-slider-01 { background-image: url($img-bg-hero-landing-01);};
			.bg-slider-02 { background-image: url($img-bg-hero-landing-02);};
			.bg-slider-03 { background-image: url($img-bg-hero-landing-03);};
			.bg-slider-04 { background-image: url($img-bg-hero-landing-04);};
			.bg-slider-05 { background-image: url($img-bg-hero-landing-05);};
		}
	}

	.container, .container-fluid {
		position: relative;
	}

	.box, .container, .container-fluid {
		z-index: 2;
	}

	.btn.btn-primary.btn-lg {
		border-radius: 50px;
	}
}

.titles {

	&:after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: url($img-bg-section-landing);
		background-attachment: fixed;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		z-index: -1;
		@include translate3d(0,0,100px);
		@include scale(1.02);
		@include transform-origin (50% 50%);
	}
}

.dispositivos {
	background-image: url($img-bg-displays-landing-mobile);
	
	background-attachment: scroll;
	background-size: cover;
	background-position: center center;

	@media (min-width: $screen-sm-min) {
		background-image: url($img-bg-displays-landing);
	}

	h2 {
	    margin-top: 135px;
	}

	/* Landscape for iphone4 +, nexus 4 + */
	@media only screen 
	and (min-device-width: 320px) 
	and (-webkit-min-device-pixel-ratio: 2)
	and (orientation: landscape) {

		background-position: center 90%;
		h2 {
			margin-top: 0;
		}
	}

	/* Landscape for iphone 6 */
	@media only screen 
	and (min-device-width: 667px) 
	and (-webkit-min-device-pixel-ratio: 2)
	and (orientation: landscape) { 
		h2 {
			margin-top: 40px;
		}
	}


	// galaxy tablet
	@media only screen
	and (min-device-width: 600px) 
	and (max-device-width: 1020px)
	and (-webkit-min-device-pixel-ratio: 1)
	and (orientation: portrait) {
		background-position: center 90%;
	}

	/* Landscape lg g2  */
	@media only screen 
	and (min-device-width: 360px) 
	and (max-device-width: 640px)
	and (-webkit-min-device-pixel-ratio:3)
	and (orientation: landscape) {
		h2 {
			margin-top: 40px;
		}
	}
}

.quotes {
	

	&:after {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: url($img-bg-quotes-landing);
		background-attachment: fixed;
		background-size: cover;
		background-position: center center;
		background-repeat: no-repeat;
		z-index: -1;
		@include translate3d(0,0,100px);
		@include scale(1.05);
		@include transform-origin (50% 50%);
	}

	.box {
		bottom: 0;
		left: 0;
		@include translate(0, 0);
	}


	h2 {

		@media (min-width: $screen-xlg-min) {
			font-size: 3.750em;
			line-height: 0.8;
		}
	}
}

.call-to-action {
	background-image: url($img-bg-calltoaction-landing);
	
	background-attachment: scroll;
	background-size: cover;
	background-position: center center;

	/* Landscape for iphone4 +, nexus 4 + */
	@media only screen 
	  and (min-device-width: 320px) 
	  and (-webkit-min-device-pixel-ratio: 2)
	  and (orientation: landscape) {
  		font-size: 0.720em;
  		.img-responsive {
  			max-width: 75%;
  			margin: 0 auto;
  		}
  	}
}

.local-movies {
	position:  relative;
	h2 {
		position: absolute;
	    top: 50%;
	    left: 50%;
	    z-index: 9;
	    color: #eca455;
	    @include translate(-50%,-50%);
	    margin: 0;
	    width: 60%;
	    text-align: center;

	    font-size: 2.5em;
		line-height: 1;
		//font-family: $raleway-bold;

		small {
			font-family: $font-family-base;
			font-size: 1em;
		}

		@media (min-width: $screen-sm-min) {
			font-size: 3.750em;
			line-height: 0.9;
		}

		@media (min-width: $screen-lg-min) {
			font-size: 4.750em;
		}

		@media (min-width: $screen-xlg-min) {
			font-size: 5.625em;
			line-height: 0.9;
		}
	}
}

body.body-parallax {
	padding-top: 0;

	#main-container {
	    overflow: hidden; //ie11 fix
	}

	.container-landing {
	    margin-top: 0;
	}
}

.next-section {
	position: fixed;
	left: 0;
    right: 0;
    bottom: 15px;
    z-index: 9;

    a {
    	outline: none;
    }

    .arrow-down {
    	@include transition(all 0.2s ease-in-out);
    	@include rotate(0);
    }

    .arrow-up {
    	display: block;
    	@include transition(all 0.2s ease-in-out);
    	@include rotate(-180);
    }
}