.create-profile {
    background-color: $bg-user-profile;
    position: relative;
    display: block;
    text-align: center;

    a {
    	display: block;
    	width: 100%;
    	padding: 0 15px;
        line-height: 77px;   
        height: 77px;

        /* portrait for iphone 6 */
        @media only screen 
        and (min-device-width : 375px) 
        and (max-device-width : 667px) 
        and (orientation : portrait) {
            line-height: 93px;   
            height: 95px;
        }

         /* portrait for iphone 6 */
        @media only screen 
        and (min-device-width : 375px) 
        and (max-device-width : 667px) 
        and (orientation : landscape) {
            line-height: 190px;   
            height: 192px;
        }

        @media (min-width: $screen-sm-min) {
            height: 144px;
            line-height: 144px;
        }

         @media (min-width: $screen-md-min) {
            height: 123px;
            line-height: 125px;
        }
    }

    [data-locked="true"] & {
        a {
            padding: 15px 0;
            height: 82px;
            line-height: 53px;  
        }
    }
}