.dropdown {

	[data-locked="true"] & {
		&:hover {
			.dropdown-menu {
				display: block;
			}
		}
	}
}

.dropdown-menu {
	top: 49px;
	font-size: 1em;
	z-index: 9999;

	&:before {
		content: '';
		width: 0;
		height: 0;
		border-left: 10px solid transparent;
		border-right: 10px solid transparent;
		border-bottom: 10px solid $bg-dropdown;
		position: absolute;
		top: -10px;
    	left: 145px;
	    transform: rotate(360deg);
	}
}

.payment-group {
	
	#collapse_stripe {
		
		.dropdown-menu {
			top: 20px;
		}
	}

	.dropdown-menu {
		top: 0px;
		left: 130px;
		background-repeat: no-repeat;
		height: 101px;
		background-position: 7px 4px;

		&.dark-theme {
			background-color: $prussian-blue;
		}

		&:before {
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			border-bottom: 10px solid $prussian-blue;
			top: 50px;
	    	left: -15px;
	    	transform: rotate(270deg);
		}

		&.other {
			background-image: url($img-other-credit-card);
		}

		&.amex {
			background-image: url($img-amex-credit-card);
		}
	}
}

.dropdown-menu.dark-theme {
	background-color: $bg-dropdown;
	border: 0;
	padding: 5px 8px;
	@include box-shadow(1px 2px 10px $bg-dropdown);
}

.dropdown-explore {
	font-family: $dropdown-font;
	font-size: 1.250em;
	position: relative;
	@include transition(all .2s ease-in-out);

	.caret {
		border: 0;

		&:after {
			content: '\f107';
			font-family: 'FontAwesome';
			position: absolute;
			right: 0;
			top: 26%;
		}
	}
}

.dropdown-tags {
	padding: 15px;
	background-color: $bg-dropdown-alter;
	border: 0;
	min-width: 200px;

	@media (min-width: $screen-sm-min) {
	   min-width: 560px;
	}

	&:before {
		left: 10%;
		border-bottom: 10px solid $bg-dropdown-alter;
	}

}

.dropdown-profile {
	text-align: right;

	.dropdown-menu {
		background-color: $bg-dropdown;
		border: 0;
		min-width: 290px;
		padding: 10px 0;
		right: -18px;
 		top: 52px;
		@include box-shadow(1px 2px 10px $bg-dropdown);

		h6 {
			display: block;
			padding: 0 20px;
			padding-bottom: 3px;
			font-size: 0.750em;
			font-weight: bold;
			border-bottom: 1px solid lighten($bg-dropdown,20);
		}

		li {
			a {
				color: $text-color;
				padding: 7px 20px;
				font-size: 0.875em;

				&:hover {
					color: $text-color;
					background-color: $bg-dropdown-hover;
				}
			}
		}

		.user-select a {
			font-family: $font-family-base;
		}

		.user-options a {
			background-color: $bg-user-options;
			font-family: $user-options-font;
		}

		&:before {
			right: 20%;
		}
	}
}


.dropup {

	span {

		.container-detail & {
			display: inline-block;
		}

		display: block;
	}
}

.dropdown-links {
	@extend .dropdown-menu;
	background-color: $bg-dropdown-alter;
	border: 0;
	min-width: 260px;
	padding: 10px 0;
	left: inherit;
	right: -13px;

	&:before {
		right: 6px;
		border-bottom: 10px solid $bg-dropdown-alter;
	}

	a {
		color: $text-color;
		@extend .menu-link-btn;
		display: block;
		font-size: 0.875em;
		padding: 15px;

		&:hover{
			background-color: $bg-dropdown-hover;
		}
	}
}

.dropdown_mobile {
	.dropdown-menu {
	    bottom: inherit;
	    right: -14px;
	    left: inherit;

	    &:after {
			border-left: 10px solid transparent;
			border-right: 10px solid transparent;
			border-bottom: 10px solid lighten($bg-media,10);
			border-top: inherit;
			position: absolute;
			bottom: inherit;
			left: inherit;
			top: -11px;
			right: 26px;
		}
	}
}
